<template>
  <label class="form-check d-flex align-items-center">
    <input
      class="form-check-input mr-2"
      type="checkbox"
      :checked="modelValue === true"
      @input="$emit('update:modelValue', !modelValue)"
    />

    <span class="form-check-label fz-12 color-grey-medium">
      {{ label }}
    </span>
  </label>
</template>

<script>
export default {
  name: 'BaseCheckbox',
  props: {
    label: {
      type: String,
      default: null
    },
    modelValue: {
      type: Boolean,
      default: false
    }
  },
  emits: ['update:modelValue']
};
</script>

<style lang="scss" scoped>
.form-check {
  cursor: pointer;
}
</style>
