<template>
  <div
    ref="modal"
    class="modal fade"
    tabindex="-1"
    aria-hidden="true"
    data-test="modal"
    :data-bs-backdrop="backgroundClose ? 'static' : true"
  >
    <div class="modal-dialog modal-dialog-centered">
      <div class="modal-content">
        <div class="modal-header justify-content-center">
          <h5 class="modal-title" data-test="title">{{ title }}</h5>
        </div>

        <button
          v-if="!hideClose"
          type="button"
          class="btn-close"
          data-bs-dismiss="modal"
          aria-label="Close"
          data-test="close"
        ></button>

        <div class="modal-body" data-test="default">
          <slot v-if="showContent"></slot>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { ref, toRef } from '@vue/reactivity';
import { onMounted, watch } from '@vue/runtime-core';
import Modal from 'bootstrap/js/dist/modal';

/** Component used for modals
 *
 *  @example
 *  <base-modal
 *   v-model="showModal"
 *   title="This is modal"
 *  >
 *   Modal content goes here
 *  </base-modal>
 *
 */

const HIDE_MODAL_CONTENT_AFTER_CLOSE_DELAY = 500;

export default {
  name: 'BaseModal',
  props: {
    title: {
      type: String,
      default: null
    },
    modelValue: {
      type: Boolean,
      default: false
    },
    backgroundClose: {
      type: Boolean,
      default: false
    },
    hideClose: {
      type: Boolean,
      default: false
    }
  },
  emits: ['update:modelValue'],
  setup(props, { emit }) {
    const modal = ref(null);
    const modelValue = toRef(props, 'modelValue');
    const currentModal = ref(null);
    const showContent = ref(false);
    const timerId = ref(null);

    watch(
      () => modelValue.value,
      (newV) => {
        if (newV) {
          showContent.value = true;
          currentModal.value.show();
          clearTimeout(timerId.value);
        } else {
          currentModal.value.hide();

          timerId.value = setTimeout(() => {
            showContent.value = false;
          }, HIDE_MODAL_CONTENT_AFTER_CLOSE_DELAY);
        }
      }
    );

    onMounted(() => {
      currentModal.value = new Modal(modal.value);

      modal.value.addEventListener('hide.bs.modal', () => {
        emit('update:modelValue', false);
      });

      modal.value.addEventListener('show.bs.modal', () => {
        emit('update:modelValue', true);
      });
    });
    return {
      modal,
      showContent
    };
  }
};
</script>

<style lang="scss" scoped>
.modal-header {
  border-bottom: none !important;
  padding-bottom: 5px;
}

.modal-content {
  @include has-border-radius;
}

.btn-close {
  position: absolute;
  right: 10px;
  top: 10px;
  opacity: 0.3 !important;
  z-index: 3;
}

.modal-body {
  padding-top: 0;
}
</style>
