            </div>
            <div class="d-wallet-btns">
              <div id="googlePay">
                <GooglePay :totalPrice="amount" @paymentData="gPaymentData = $event; showCC = false"/>
              </div>
              <div id="applePay">
                <ApplePay :totalPrice="amount" />
              </div>
            </div>
          </template>
          
        </base-tab>

        <base-tab
          v-if="
            isBankPaymentAvailable &&
            paymentOptions.includes(PAYMENT_METHODS.BANK)
          "
          class="mt-2 payment-tab"
          :model-value="paymentMethod === PAYMENT_METHODS.BANK"
        >
          <!-- @change="triggerIframeHeightResize" -->

          <template #header>
            <base-radio
              v-model="paymentMethod"
              :custom-style="radioStyle"
              label="Bank account"
              :value="PAYMENT_METHODS.BANK"
            />
          </template>

          <template #default>
            <div class="pt-2 d-flex">
              <base-input
                v-model="accountNumber"
                :error="accountNumberError"
