                :custom-style="inputStyle"
                :options="ACCOUNT_TYPE_OPTIONS"
                placeholder="Account Type"
                class="value-input w-100"
                @blur="validateAccountType"
              />
            </div>
          </template>
        </base-tab>

        <base-tab
          v-if="paymentOptions.includes(PAYMENT_METHODS.CRYPTO)"
          :model-value="paymentMethod === PAYMENT_METHODS.CRYPTO"
          class="mt-2 payment-tab"
        >
          <!-- @change="triggerIframeHeightResize" -->

          <template #header>
            <base-radio
              v-model="paymentMethod"
              :custom-style="radioStyle"
              label="Crypto currency"
              :value="PAYMENT_METHODS.CRYPTO"
            >
              <template #default>
                <p class="fz-12 fw-400 powered-text">Powered by: CurPay</p>
              </template>
            </base-radio>
          </template>
          <template #default>
            <div class="credit-card pt-2">
              <crypto-payment
                v-model="selectedCryptoType"
                :custom-style="cryptoCheckboxStyle"
                :input-style="inputStyle"
                :crypto-options="cryptoCurrenciesList"
                :loading="loadingCryptoOptions"
              />

              <transition name="slide-y">
                <p
                  v-if="selectedCryptoTypeError"
                  class="default-error-text mt-1 color-danger fz-14 pl-2 mt-1 fw-bolder"
                >
                  {{ selectedCryptoTypeError }}
                </p>
              </transition>

              <transition name="slide-y">
                <p
                  v-if="cryptoError"
                  class="default-error-text mt-1 color-danger fz-14 pl-2 mt-1 fw-bolder"
