                </p>
              </transition>
            </div>
          </template>
        </base-tab>

        <base-tab
          v-if="paymentOptions.includes(PAYMENT_METHODS.TERMINAL)"
          :model-value="paymentMethod === PAYMENT_METHODS.TERMINAL"
          class="mt-2 payment-tab"
        >
          <!-- @change="triggerIframeHeightResize" -->

          <template #header>
            <base-radio
              v-model="paymentMethod"
              :custom-style="radioStyle"
              label="Terminal"
              :value="PAYMENT_METHODS.TERMINAL"
            />
