          <template #default>
            <div class="pt-2 pl-2">
              <p class="fz-14 color-grey-medium fw-500">
                Click on "Pay" and process you payment with terminal
              </p>
            </div>

            <transition name="slide-y">
              <p
                v-if="laneIdError"
                class="default-error-text mt-1 color-danger fz-14 pl-2 mt-1 fw-bolder"
              >
                {{ laneIdError }}
              </p>
            </transition>
          </template>
        </base-tab>

        <transition name="slide-y">
          <div v-if="allowTip" class="pt-2 px-2">
            <p class="fw-500 fz-14 color-grey-dark">Leave a tip</p>
            <div class="d-flex">
              <base-button
                :custom-style="buttonStyle"
                class="w-25 mr-2"
                :color="Number(tipPercent) === 0 ? 'primary' : 'grey-medium'"
                @click="handlePercentTipClick(0, $event)"
              >
                0%
              </base-button>

              <base-button
                :custom-style="buttonStyle"
                class="w-25 mr-2"
                :color="Number(tipPercent) === 10 ? 'primary' : 'grey-medium'"
                @click="handlePercentTipClick(10, $event)"
              >
                10%
              </base-button>

              <base-button
                :custom-style="buttonStyle"
                class="w-25 mr-2"
                :color="Number(tipPercent) === 15 ? 'primary' : 'grey-medium'"
                @click="handlePercentTipClick(15, $event)"
              >
                15%
              </base-button>

              <base-button
                :custom-style="buttonStyle"
                class="w-25"
                :color="Number(tipPercent) === 20 ? 'primary' : 'grey-medium'"
                @click="handlePercentTipClick(20, $event)"
              >
                20%
              </base-button>
            </div>

            <base-input
              v-model="tipValue"
              :custom-style="inputStyle"
              placeholder="Tip, $"
              label="Tip, $"
              class="value-input w-100 mt-2"
              type="number"
              step="0.01"
              @update:modelValue="handleCustomTipInput"
            />
          </div>
        </transition>

        <div class="pt-2">
          <div v-if="terms" class="pb-2 text-center">
            <a
              :href="terms.url"
              class="fw-500 color-dark fz-14 text-center terms"
              target="_blank"
            >
              {{ terms.text }}</a
            >
          </div>

          <div>
            <transition name="slide-y">
              <p
                v-if="
                  paymentMethod === PAYMENT_METHODS.CRYPTO && !this.email
                "
                class="default-error-text mt-1 color-danger fz-14 pl-2 mt-1 fw-bold"
              >
                Please enter an email address.
              </p>
            </transition>
          </div>

          <base-button
            class="w-100"
            :custom-style="buttonStyle"
            :disabled="isSuccess"
            type="submit"
          >
            {{ buttonText }}
          </base-button>

          <p
            v-if="paymentType === PAYMENT_TYPES.SUBSCRIPTION"
            class="fz-12 color-grey-medium pl-2 pt-1 italic"
          >
            *by submitting this payment you agree to keep your secure card token
            on file
          </p>

          <transition name="slide-y">
            <p
              v-if="paymentMethod === PAYMENT_METHODS.CRYPTO"
              class="fz-12 color-grey-medium pl-2 pt-1 italic"
            >
              *Gas/Miner Fees - Payee is responsible for all GAS fees.
            </p>
          </transition>
        </div>
      </div>
    </form>
  </div>
</template>

<script>
import BaseInput from '@/components/base/BaseInput.vue';
import BaseDropdown from '@/components/base/BaseDropdown.vue';
import BaseButton from '@/components/base/BaseButton.vue';
import BaseSpinner from '@/components/base/BaseSpinner.vue';
import BaseModal from '@/components/base/BaseModal.vue';
import ProductsDescription from '@/components/payment-page/ProductsDescription.vue';
import CryptoPayment from '@/components/payment-page/CryptoPayment.vue';
import CryptoQr from '@/components/payment-page/CryptoQr.vue';
import BaseTab from '@/components/base/BaseTab.vue';
import BaseRadio from '@/components/base/BaseRadio.vue';
import SavedCreditCards from '@/components/payment-page/SavedCreditCards.vue';
import AddressSection from '@/components/payment-page/AddressSection.vue';
import CustomerNameSection from '@/components/payment-page/CustomerNameSection.vue';
import BillingAddressBox from '@/components/payment-page/BillingAddress.vue';
import BaseCheckbox from '@/components/base/BaseCheckbox.vue';
import GooglePay from '@/components/payment-page/GooglePay.vue';
import ApplePay from '@/components/payment-page/ApplePay.vue';

import { computed, ref } from '@vue/reactivity';
import { useRoute, useRouter } from 'vue-router';
import { nextTick, onBeforeUnmount, onMounted } from '@vue/runtime-core';
import { checkSendUserEmail, checkReadOnlyEmail } from '@/helpers';

import useFormat from '@/composables/payment/format.js';
import useValidate from '@/composables/payment/validate.js';
import messanger from '@/services/messages';
import useParentWindow from '@/composables/common/parent-window';
import useService from '@/composables/common/services';
import useDataFields from '@/composables/payment/dataFields.js';
import usePaymentType from '@/composables/payment/paymentType.js';
import useSaveCardToken from '@/composables/payment/saveCardToken.js';
import {
  PAYMENT_METHODS,
  PAYMENT_TYPES,
  DEFAULT_PAYMENT_ERROR_MESSAGE,
  CARD_TOKEN_MODES_ARRAY,
  CARD_TOKEN_MODES,
  REPLACE_PERCENT_VALUE,
  DEFAULT_TIP_PERCENT,
  DEFAULT_ZIPMODE_VALUE,
  ZIP_MODE_VALUES,
  ACCOUNT_TYPE_OPTIONS,
  DEFAULT_LABELS_MODE
} from '@/config/constants.js';
import { getParsedData } from '@/helpers';
import useSubscriptionType from '@/composables/payment/subscriptionType.js';
import usePaymentButtonText from '@/composables/payment/paymentButtonText.js';
import useCryptoPayment from '@/composables/payment/cryptoPayment.js';
import useCryptoCurrencies from '@/composables/payment/cryptoCurrencies.js';
import useErrorStyles from '@/composables/payment/errorStyles.js';
import useTokenPayment from '@/composables/payment/tokenPayment.js';
import useTip from '@/composables/payment/tip.js';
import useResize from '@/composables/common/resize';
import useClientDetails from '@/composables/payment/clientDetails.js';
import useTerms from '@/composables/payment/terms.js';
import useAdditionalFields from '@/composables/payment/additionalFields.js';

export default {
  name: 'CardInputPage',
  components: {
    BaseInput,
    BaseDropdown,
    BaseButton,
    BaseSpinner,
    BaseTab,
    BaseRadio,
    ProductsDescription,
    CryptoPayment,
    BaseModal,
    CryptoQr,
    SavedCreditCards,
    AddressSection,
    CustomerNameSection,
    BaseCheckbox,
    BillingAddressBox,
    GooglePay,
    ApplePay
  },
  setup() {

    // im just gonna use this space to write some utility functions...
    
    // the function below will handle normal credit card input
    // and vendors like Google Pay and Apple Pay given a form validator result
    const invalid = (result, showCC) => {
      if (showCC.value === false) { // not using credit card (using gpay / apay)
        // check the errors array manually
        // for now make sure the phone number and email are being checked
        return "email" in result.errors || "phoneNumber" in result.errors; // if either are invalid the whole thing is invalid
      } else {
        return ! result.valid; // otherwise just use the validator normally
      }
    };

    const isLoading = ref(false);
    const transactionId = ref(null);
    const amount = ref(null);
    const ticket = ref(null);
    const productsDescription = ref(null);
    const isSuccess = ref(false);
    const newWindow = ref(null);
    const autocomplete = ref(true);
    const labels = ref(DEFAULT_LABELS_MODE);
    const route = useRoute();
    const router = useRouter();

    const {
      resizeWindowOnUpdate,
      updateIframeHeight,
      triggerDocumentClass,
      getResizeOnUpdateValue
    } = useResize({
      newWindow
    });

    // for digital wallet onclick
    const showCC = ref(true);
    const gPaymentData = ref(null);
    const aPaymentData = ref(null);

    // params value or '{}' in base64
    // const queryData = route.query.params || BRACES_IN_BASE64;
    const cleared = ref(false);

    const {
      validate,
      cardNumber,
      date,
      cvc,
      cardNumberError,
      dateError,
      cvcError,
      zipCodeError,
      zipCode,
      validateDate,
      validateCvc,
      validateCardNumber,
      validateFirstName,
      validateLastName,
      validateZip,
      setErrors,
      accountNumber,
      accountNumberError,
      validateAccountNumber,
      routingNumber,
      routingNumberError,
      validateRoutingNumber,
      accountType,
      accountTypeError,
      validateAccountType,

      paymentMethod,
      validateEmail,

      // Terminal
      laneId,
      laneIdError,

      // Selected credit card token
      savedPaymentToken,
      securedTokenCvc,
      securedTokenCvcError,
      tokenMode,

      // charge or subcsription
      paymentType,
      paymentOptions,

      selectedCryptoType,
      selectedCryptoTypeError,

      // tip
      allowTip,
      tipValue,
      tipPercent,

      // // show email field

      email,
      emailError,

      // show email,
      emailReadOnly,

      // send email
      sendEmail,

      // zipmode(zip required or hidden)
      zipMode,

      // address
      addressOptions,
      address,

      billingOptions,
      billingAddress,
      // showBillingAddress,

      // customer details
      showFullName,
      fullNameDetails,
      errors,

      phoneOption,
      phoneNumber,
      validatePhoneNumber,

      signUpUser,
      emailOption,
      savePaymentTokenIsAvailable
    } = useValidate();

    const { saveCreditCardToken } = useSaveCardToken(
      savePaymentTokenIsAvailable
    );

    const {
      fetchCryptoCurrencies,
      cryptoCurrenciesList,
      loadingCryptoOptions
    } = useCryptoCurrencies();
    const { handlePercentTipClick, handleCustomTipInput, setDefaultPercent } =
      useTip({
        amount,
        tipValue,
        tipPercent
      });

    // const parsed = JSON.parse(decodeURIComponent(atob(queryData)));

    const parsed = getParsedData(route.query.params);

    // check if need to resize payment form
    // on payment method update(on new tab expand)
    getResizeOnUpdateValue(parsed);

    // console.log(parsed);
    newWindow.value = parsed.newWindow;

    emailReadOnly.value = checkReadOnlyEmail({
      initialEmailValue: parsed.email
    });

    const { terms } = useTerms(parsed);
    const { optIn, showEmailField, showSaveCardOption, showDescription } =
      useAdditionalFields({
        parsed,
        paymentType,
        signUpUser,
        // showBillingAddress,
        showFullName,
        phoneOption,
        emailReadOnly,
        emailOption,
        savePaymentTokenIsAvailable
      });
    const { isBankPaymentAvailable } = usePaymentType(parsed, paymentType);
    const {
      colorsDataIsLoading,
      fetchClientDetails,
      buttonStyle,
      inputStyle,
      radioStyle,
      cryptoCheckboxStyle,
      errorStyle
    } = useClientDetails({ parsed });

    if (CARD_TOKEN_MODES_ARRAY.includes(parsed.tokenMode)) {
      tokenMode.value = parsed.tokenMode;
    }

    if (parsed.terminal) {
      laneId.value = parsed.terminal;
    }

    if (parsed.autocomplete === false) {
      autocomplete.value = false;
    }

    if (parsed.labels) {
      labels.value = parsed.labels;
    }

    addressOptions.value = parsed.address;
    billingOptions.value = parsed.billingAddress;

    zipMode.value =
      parsed.zipMode !== undefined ? parsed.zipMode : DEFAULT_ZIPMODE_VALUE;

    const {
      start,
      cycles,
      interval,
      frequency,
      trial,
      email: emailFromParams
    } = useSubscriptionType(parsed, paymentType);

    const { buttonText } = usePaymentButtonText({
      tipValue,
      selectedCryptoType,
      paymentMethod,
      amount,
      paymentType,
      frequency,
      interval,
      cycles,
      trial,
      start,
      tokenMode,
      parsed
    });

    const {
      showCryptoPaymentModal,
      qrImage,
      cryptoData,
      processCryptoPayment,
      timer,
      timerId,
      cryptoError
    } = useCryptoPayment(
      selectedCryptoType,
      amount,
      email,
      newWindow,
      tipValue,
      allowTip,
      address,
      addressOptions,
      billingOptions,
      billingAddress
    );

    if (emailFromParams) {
      email.value = emailFromParams;
    }

    const { tokensList, tokensListLoading, getAvailableTokens } =
      useTokenPayment(email);
    const { apiService } = useService();

    const { errorCssVariablesList, errorClasses } = useErrorStyles(errorStyle);

    const { formatCardNumber, formatDate, onlyDigit } = useFormat(date);
    const { emitToParentWindow } = useParentWindow(parsed);

    // payment method
    // CARD OR BANK ACCOUNT
    const { data } = useDataFields(paymentMethod, {
      newWindow: newWindow,
      emailFromParams: emailFromParams,

      // Provide saved token if exists
      savedPaymentToken: savedPaymentToken,
      securedTokenCvc: securedTokenCvc,
      tokenMode,
      email: email,

      // Terminal payments
      laneId,

      // Credit card payments
      cardNumber: cardNumber,
      date: date,
      cvc: cvc,
      zipCode: zipCode,

      // only for charge method
      accountNumber,
      routingNumber,
      accountType,

      // common field
      amount: amount,
      signUpUser: signUpUser,

      // subscription fields
      start,
      // end,
      cycles,
      interval,
      frequency,
      trial,

      // payment type
      // CHARGE OR SUBSCRIPTION
      paymentType,

      // tip
      tipValue,
      allowTip,

      //address
      address,
      addressOptions,

      // customer details
      fullNameDetails,
      phoneNumber,

      billingAddress,
      billingOptions,

      fee: parsed.fee,

      // custom meta
      meta: parsed.meta,

      // saved meta id
      meta_id: parsed.meta_id,

      // custom source
      source: parsed.source
    });

    const removePaymentMethod = (methodName) => {
      const cryptoIndex = paymentOptions.value.indexOf(methodName);

      if (cryptoIndex >= 0) {
        paymentOptions.value.splice(cryptoIndex, 1);
      }
    };

    if (parsed.amount) {
      amount.value = (+parsed.amount).toFixed(2);
    }

    transactionId.value = parsed.transactionId;
    ticket.value = parsed.ticket;

    allowTip.value = !!parsed.allowTip;

    if (allowTip.value) {
      setDefaultPercent(DEFAULT_TIP_PERCENT);
    }

    const descr = (parsed.description || '').replaceAll(
      REPLACE_PERCENT_VALUE,
      '%'
    );
    productsDescription.value = descr && JSON.parse(descr);
    paymentOptions.value = parsed.paymentOptions || [];

    // Crypto & terminal don't support save feature, so remove it
    if (tokenMode.value === CARD_TOKEN_MODES.SAVE) {
      removePaymentMethod(PAYMENT_METHODS.CRYPTO);
      removePaymentMethod(PAYMENT_METHODS.TERMINAL);
    }

    paymentMethod.value =
      (paymentOptions.value && paymentOptions.value[0]) || PAYMENT_METHODS.CARD;

    const generateResponse = (obj) => {
      return { ...obj };
    };

    const handleErrorResponse = (err) => {
      if (!newWindow.value) {
        // emit to parent window, current - is iframe
        messanger.send('error', generateResponse(err));
      } else {
        // emit to parent separate window
        emitToParentWindow(generateResponse(err));
      }
    };

    const handleChangeCryptoModalState = (value) => {
      if (!value) {
        clearInterval(timerId.value);
      }

      // triggerIframeHeightResize();
    };

    const isFromGeneratedLink = !!parsed.fromLink;

    // allow this method only for card
    const savePaymentTokenAfterSuccess = computed(() => {
      if (
        paymentMethod.value !== PAYMENT_METHODS.CARD
        // && paymentMethod.value !== PAYMENT_METHODS.BANK
      ) {
        return false;
      }

      if (!savePaymentTokenIsAvailable.value) {
        return false;
      }

      if (savedPaymentToken.value) {
        return false;
      }

      if (tokenMode.value !== CARD_TOKEN_MODES.CHARGE) {
        return false;
      }

      return true;
    });

    const processSaveCreditCardToken = () => {
      isLoading.value = true;

      saveCreditCardToken({
        cc: data.value.cc,
        mm: data.value.mm,
        yy: data.value.yy,
        cvc: data.value.cvv,
        zip: data.value.zip || '',
        id: data.value.email,
        accountNumber: data.value.accountNumber,
        routingNumber: data.value.routingNumber,
        type: data.value.TYPE,
        meta_id: parsed.meta_id,
        meta: parsed.meta
      })
        .then((res) => {
          // localStorage.setItem(SAVED_EMAIL_KEY, data.value.email);
          const successData = {
            token: res.id,
            status: true
          };

          if (amount.value) {
            successData.amount = amount.value;
          }

          isLoading.value = false;
          isSuccess.value = true;

          if (isFromGeneratedLink) {
            router.push('/success?savedToken=true');
            return;
          }

          if (!newWindow.value) {
            messanger.send('success', generateResponse(successData));
          } else {
            // emit to parent separate window
            emitToParentWindow(generateResponse(successData));
          }
        })
        .catch((err) => {
          const message =
            typeof err.message === 'object' && err.message
              ? err.message.result
              : err.message;

          setErrors({
            securedTokenCvc: message || DEFAULT_PAYMENT_ERROR_MESSAGE
          });

          isLoading.value = false;
        });
    };

    const handleSubmit = async () => {
      if (cleared.value) {
        return;
      }

      const result = await validate();

      if (invalid(result, showCC)) {
        console.log("ERROR:", result, showCC);
        // showMessage({
        //   title: 'Error',
        //   text: "Please check the information you've entered.",
        //   type: 'danger'
        // });
        return;
      }

      if (showCC.value === false) {
        if (gPaymentData.value) {
          console.log("google pay successful:", gPaymentData.value);
        } else if (aPaymentData.value) {
          console.log("apple pay successful:", aPaymentData.value);
        }
        return;
      }

      if (tokenMode.value === CARD_TOKEN_MODES.SAVE) {
        processSaveCreditCardToken();

        return;
      }

      if (paymentMethod.value === PAYMENT_METHODS.CRYPTO) {
        isLoading.value = true;

        processCryptoPayment(paymentType.value)
          .then(() => {
            isLoading.value = false;
          })
          .catch(() => {
            isLoading.value = false;
          });
        return;
      }

      isLoading.value = true;

      apiService
        .post(paymentType.value, data.value)
        .then((res) => {
          isLoading.value = false;
          isSuccess.value = true;

          // if user used credit card details
          // save them
          if (savePaymentTokenAfterSuccess.value) {
            saveCreditCardToken({
              cc: data.value.cc,
              mm: data.value.mm,
              yy: data.value.yy,
              zip: data.value.zip,
              id: data.value.email,
              accountNumber: data.value.accountNumber,
              routingNumber: data.value.routingNumber,
              type: data.value.TYPE
            });
          }

          // if (email.value) {
          //   localStorage.setItem(SAVED_EMAIL_KEY, email.value);
          // }

          if (isFromGeneratedLink) {
            router.push('/success');
            return;
          }

          if (!newWindow.value) {
            messanger.send('success', generateResponse(res));
          } else {
            // emit to parent separate window
            emitToParentWindow(generateResponse(res));
          }
        })
        .catch((err) => {
          isLoading.value = false;

          if (paymentMethod.value === PAYMENT_METHODS.BANK) {
            setErrors({
              accountNumber:
                err.message?.result || DEFAULT_PAYMENT_ERROR_MESSAGE
            });
          } else {
            const message =
              typeof err.message === 'object' && err.message
                ? err.message.result
                : err.message;

            if (savedPaymentToken.value) {
              setErrors({
                securedTokenCvc: message || DEFAULT_PAYMENT_ERROR_MESSAGE
              });
              return;
            }

            if (paymentMethod.value === PAYMENT_METHODS.TERMINAL) {
              setErrors({
                laneId: message || DEFAULT_PAYMENT_ERROR_MESSAGE
              });
              return;
            }

            setErrors({
              cardNumber: message || DEFAULT_PAYMENT_ERROR_MESSAGE
            });
          }

          if (isFromGeneratedLink) {
            return;
          }

          handleErrorResponse({ ...err, status: false });
        });
    };

    const handleClearEvent = () => {
      cleared.value = true;
    };

    const onTokensTabTrigger = (v) => {
      // triggerIframeHeightResize();

      if (!v) {
        savedPaymentToken.value = null;
      }
    };

    onMounted(() => {
      // we need to fetch client color scheme
      fetchClientDetails();

      messanger.subscribe('clear', handleClearEvent);

      sendEmail.value = checkSendUserEmail({
        initialEmailValue: parsed.email,
        paymentType: parsed.paymentType
      });

      // console.log('+++');
      // console.log(sendEmail.value);

      if (
        paymentOptions.value.includes(PAYMENT_METHODS.CARD) &&
        email.value &&
        tokenMode.value === CARD_TOKEN_MODES.CHARGE
      ) {
        getAvailableTokens();
      }

      if (paymentOptions.value.includes(PAYMENT_METHODS.CRYPTO)) {
        fetchCryptoCurrencies();
      }

      // update iframe height depending on content height
      // we use nextTick as need to wait to calculating results first
      nextTick(() => {
        updateIframeHeight();
        triggerDocumentClass(parsed);
      });
    });

    onBeforeUnmount(() => {
      messanger.unsubscribe('clear', handleClearEvent);
    });

    return {
      // colors
      colorsDataIsLoading,
      //resize
      resizeWindowOnUpdate,

      // tokens tab trigger
      onTokensTabTrigger,

      // terminal
      laneIdError,
      laneId,

      // card token payment
      savedPaymentToken,
      tokensList,
      tokensListLoading,
      securedTokenCvc,
      securedTokenCvcError,
      tokenMode,

      // crypto payment data
      showCryptoPaymentModal,
      selectedCryptoType,
      cryptoCurrenciesList,
      timer,
      loadingCryptoOptions,
      cryptoError,

      // available payment options
      paymentOptions,

      // data fields
      paymentType,
      paymentMethod,
      cardNumber,
      date,
      cvc,
      zipCode,
      email,
      amount,
      routingNumber,
      accountNumber,
      accountType,

      isLoading,
      isBankPaymentAvailable,
      buttonText,
      qrImage,
      cryptoData,

      // errors
      cardNumberError,
      cvcError,
      dateError,
      emailError,
      routingNumberError,
      accountNumberError,
      accountTypeError,
      zipCodeError,
      selectedCryptoTypeError,

      //formatter
      formatCardNumber,
      formatDate,
      onlyDigit,

      //validators
      validateCardNumber,
      validateFirstName,
      validateLastName,
      validateDate,
      validateEmail,
      validateAccountNumber,
      validateRoutingNumber,
      validateAccountType,
      validateCvc,
      validateZip,
      showCC,

      // submit
      handleSubmit,

      // change iframe size
      handleChangeCryptoModalState,

      // system values
      newWindow,
      isSuccess,
      buttonStyle,
      inputStyle,
      errorCssVariablesList,
      errorClasses,
      radioStyle,
      cryptoCheckboxStyle,
      productsDescription,
      autocomplete,
      savePaymentTokenIsAvailable,
      signUpUser,
      parsed,
      labels,

      // tip
      allowTip,
      tipValue,
      tipPercent,
      handlePercentTipClick,
      handleCustomTipInput,

      // // show email
      // showEmail,

      // send email
      sendEmail,
      showEmailField,
      emailReadOnly,
      showSaveCardOption,

      // make zip required or disabled
      zipMode,

      // address
      addressOptions,
      address,
      errors,

      billingOptions,
      billingAddress,

      // customer details
      showFullName,
      fullNameDetails,
      phoneOption,
      phoneNumber,
      validatePhoneNumber,
      // terms
      terms,

      //additional fields
      optIn,
      showDescription,

      // constants
      PAYMENT_METHODS,
      PAYMENT_TYPES,
      ZIP_MODE_VALUES,
      ACCOUNT_TYPE_OPTIONS,

      gPaymentData,
      aPaymentData
    };
  },
  // methods: {
  //   changeShowCC() {
  //     showCC.value = !showCC.value
  //   }
  // }
};
</script>

