<template>
  <div>
    <ul class="saved-cards">
      <li
        v-for="token in tokensWithImages"
        :key="token.token"
        class="saved-card"
        :class="{
          selected: modelValue && modelValue.token === token.token
        }"
        @click="handleCardClick(token)"
      >
        <span class="card-logo"> <img :src="token.image" alt="" /></span
        ><span class="fz-16 color-dark"> {{ token.walletNumber }}</span>
      </li>
    </ul>
  </div>
</template>

<script>
import { ref, toRefs } from '@vue/reactivity';

export default {
  name: 'SavedCreditCards',
  props: {
    modelValue: {
      type: Object,
      default: null
    },
    securedCvc: {
      type: String,
      default: null
    },
    tokens: {
      type: Array,
      default: () => []
    }
  },
  emits: ['update:modelValue'],
  setup(props, { emit }) {
    const { modelValue, tokens } = toRefs(props);
    const tabIsVisible = ref(true);

    const handleCardClick = (value) => {
      if (modelValue.value && modelValue.value.token === value.token) {
        emit('update:modelValue', null);
        return;
      }

      emit('update:modelValue', value);
    };

    const tokensWithImages = tokens.value.map((el) => {
      let image;

      try {
        image = require(`@/assets/img/${el.card}.svg`);
      } catch (e) {
        image = require(`@/assets/img/cc.svg`);
      }

      return {
        ...el,
        image
      };
    });

    const handleTabClick = () => {
      tabIsVisible.value = !tabIsVisible.value;

      if (!tabIsVisible.value) {
        emit('update:modelValue', null);
      }
    };

    return {
      tokensWithImages,

      handleCardClick,
      tabIsVisible,
      handleTabClick
    };
  }
};
</script>

