import { useRoute } from 'vue-router';
import apiService from '@/services/api-service';
import HttpService from '@/services/api-service/http.js';
import WebsocketService from '@/services/api-service/websocket.js';
import { getParsedData } from '@/helpers';

apiService.setServices([WebsocketService, HttpService]);

export default function () {
  const route = useRoute();

  const parsed = getParsedData(route.query.params);

  const token = parsed.apikey || route.params.text;
  // const token = '72653c60-4fd5-440f-827e-c1a73f06aa3a';

  apiService.init({
    token: token
  });

  return {
    apiService
  };
}
