import connect from '@/services/websocket';

const TIMEOUT = 30000;

class WebsocketService {
  constructor({ token }) {
    this.service = connect;
    this.token = token;
    this.client = null;
    this.available = false;
    this.pendingCalls = {};
    this.name = 'websocket';
  }

  runPingPong() {
    this.intervalId = setInterval(() => {
      const currData = {
        option: 'ping',
        apikey: this.token
      };

      // const timeoutId = setTimeout(() => {
      //   reject({ message: 'Something went wrong' });
      // }, TIMEOUT);

      this.client.send(JSON.stringify(currData));
    }, 3000);
  }

  clearPingPong() {
    clearInterval(this.intervalId);
  }

  init() {
    return new Promise((resolve, reject) => {
      try {
        let addy = `wss://${window.location.host}/ws/api`
        if (window.location.host.includes('localhost')) {addy = `wss://tripleplaypay.com/ws/api` }
        this.client = this.service( addy );
        this.intervalId = null;

        this.client.onopen = () => {
          this.available = true;
          // this.runPingPong();
          resolve();
        };

        this.client.onmessage = (e) => {
          this.handleMessage(e.data);
        };

        this.client.onerror = (e) => {
          console.log('Error');
          console.log(e);
          this.available = false;
          this.clearPingPong();
          reject();
        };

        this.client.onclose = () => {
          this.available = false;
          this.clearPingPong();
          reject();

          // this.init();
        };
      } catch (e) {
        console.log(e);
        this.available = false;
        reject();
      }

      return this.available;
    });
  }

  handleMessage(data) {
    const parsed = JSON.parse(data);
    const method = Object.keys(parsed)[0];

    if (method === 'ping' || method === 'pong') {
      return;
    }

    const handlers = this.pendingCalls[method];

    if (!handlers) {
      return;
    }

    const messageData = parsed[method];

    clearTimeout(handlers.timeoutId);

    if (!messageData) {
      return handlers.reject(messageData);
    }

    if (typeof messageData.status === 'boolean' && !messageData.status) {
      return handlers.reject(messageData);
    }

    handlers.resolve(messageData);

    delete this.pendingCalls[method];
  }

  get(url) {
    return new Promise((resolve, reject) => {
      const urlData = url.split('/');
      const [method, path] = urlData;

      const currData = {
        [method]: {
          option: path
        },
        apikey: this.token
      };

      const timeoutId = setTimeout(() => {
        reject({ message: 'Something went wrong' });
      }, TIMEOUT);

      this.client.send(JSON.stringify(currData));
      this.pendingCalls[method] = {
        resolve,
        reject,
        timeoutId
      };
    });
  }

  post(url, data, customTimeout, customApikey) {
    return new Promise((resolve, reject) => {
      const currData = {
        [url]: {
          ...data
        },
        apikey: customApikey || this.token
      };

      const timeoutId = setTimeout(() => {
        reject({ message: 'Something went wrong' });
      }, customTimeout || TIMEOUT);

      this.client.send(JSON.stringify(currData));
      this.pendingCalls[url] = {
        resolve,
        reject,
        timeoutId
      };
    });
  }
}

export default WebsocketService;
