const listeners = {};

const handleMessage = (event) => {
  const currentCell = listeners[event.data.action];

  if (currentCell) {
    for (let i = 0; i < currentCell.length; i++) {
      currentCell[i](event.data);
    }
  }
};

const globalSubscribe = () => {
  window.onmessage = function (event) {
    handleMessage(event);
  };
};

globalSubscribe();

const props = {
  unsubscribe(action, handler) {
    const currListenersList = listeners[action] || [];

    const index = currListenersList.findIndex(
      (currHandler) => currHandler === handler
    );

    if (index > -1) {
      currListenersList.splice(index, 1);
    }
  },
  subscribe(action, handler) {
    if (listeners[action]) {
      listeners[action].push(handler);
    } else {
      listeners[action] = [];
      listeners[action].push(handler);
    }
  },
  send(action, payload = {}) {
    // was window.top
    window.parent.postMessage(JSON.stringify({ action, payload }), '*');
  }
};

export default props;
