import { dateIsValid, isCardNumberValid } from '@/helpers/validators';

import { snakeCaseToCamelCase } from '@/helpers';

import * as yup from 'yup';
import {
  PAYMENT_METHODS,
  PAYMENT_TYPES,
  CARD_TOKEN_MODES
} from '@/config/constants.js';

const addressOptions = yup.lazy((value) => {
  if (!value) {
    return yup.bool().nullable();
  }

  if (typeof value === 'boolean') {
    return yup.bool().nullable();
  }

  if (Array.isArray(value)) {
    return yup.array();
  }

  if (typeof value === 'object' && value) {
    return yup.object();
  }
});

const validateAddress = (key) => {
  return yup.object().when(key, (addressOptions, schema) => {
    if (!addressOptions) {
      return schema.nullable();
    }

    // if address === true, make all fields required
    if (addressOptions === true) {
      return schema.shape({
        country: yup.string().required('Field is required').nullable(),
        firstName: yup.string().required('Field is required').nullable(),
        lastName: yup.string().required('Field is required').nullable(),
        address_2: yup.string().required('Field is required').nullable(),
        address: yup.string().required('Field is required').nullable(),
        city: yup.string().required('Field is required').nullable(),
        state: yup.string().required('Field is required').nullable(),
        zip: yup.string().required('Field is required').nullable()
      });
    }

    if (Array.isArray(addressOptions)) {
      const dynamicAddress = addressOptions.reduce((prev, curr) => {
        return {
          ...prev,
          [snakeCaseToCamelCase(curr)]: yup
            .string()
            .required('Field is required')
            .nullable()
        };
      }, {});

      return schema.shape(dynamicAddress);
    }
  });
};

const validationSchema = yup.object().shape({
  paymentOptions: yup.array(),
  savedPaymentToken: yup.object().nullable(),
  tokenMode: yup.string().nullable(),
  paymentMethod: yup.string().required('Payment type is required'),
  laneId: yup.string().nullable(),
  securedTokenCvc: yup
    .string()
    .when(
      ['paymentMethod', 'savedPaymentToken'],
      (paymentMethod, savedPaymentToken, schema) => {
        if (!savedPaymentToken) {
          return schema.nullable();
        }

        if (savedPaymentToken.type === 'bank') {
          return schema.nullable();
        }

        return schema.required('CVV is required').nullable();
      }
    ),
  cardNumber: yup
    .string()
    .when(
      ['paymentMethod', 'savedPaymentToken'],
      (paymentMethod, savedPaymentToken, schema) => {
        if (paymentMethod === PAYMENT_METHODS.BANK) {
          return schema.nullable();
        }

        if (paymentMethod === PAYMENT_METHODS.TERMINAL) {
          return schema.nullable();
        }

        if (paymentMethod === PAYMENT_METHODS.CRYPTO) {
          return schema.nullable();
        }

        if (savedPaymentToken && savedPaymentToken.token) {
          return schema.nullable();
        }

        return schema
          .test('is-card-number-valid', 'Card number is not valid', (value) => {
            return isCardNumberValid(value);
          })
          .required('Card number is required')
          .nullable();
      }
    ),
  cvc: yup
    .string()
    .when(
      ['paymentMethod', 'savedPaymentToken', 'tokenMode'],
      (paymentMethod, savedPaymentToken, tokenMode, schema) => {
        if (paymentMethod === PAYMENT_METHODS.CRYPTO) {
          return schema.nullable();
        }

        if (paymentMethod === PAYMENT_METHODS.BANK) {
          return schema.nullable();
        }

        if (paymentMethod === PAYMENT_METHODS.TERMINAL) {
          return schema.nullable();
        }

        if (savedPaymentToken && savedPaymentToken.token) {
          return schema.nullable();
        }

        return schema.required('CVV is required').nullable();
      }
    ),
  date: yup
    .string()
    .when(
      ['paymentMethod', 'savedPaymentToken'],
      (paymentMethod, savedPaymentToken, schema) => {
        if (paymentMethod === PAYMENT_METHODS.CRYPTO) {
          return schema.nullable();
        }

        if (paymentMethod === PAYMENT_METHODS.BANK) {
          return schema.nullable();
        }

        if (paymentMethod === PAYMENT_METHODS.TERMINAL) {
          return schema.nullable();
        }

        if (savedPaymentToken && savedPaymentToken.token) {
          return schema.nullable();
        }

        return schema
          .required('Date is required')
          .nullable()
          .test('is-date-valid', 'Date is not valid', (value) => {
            return dateIsValid(value);
          });
      }
    ),
  zipCode: yup
    .string()
    .when(['zipMode', 'paymentMethod'], (zipMode, paymentMethod, schema) => {
      if (paymentMethod !== PAYMENT_METHODS.CARD) {
        return schema.nullable();
      }

      if (zipMode === 'required') {
        return schema.nullable().required('Field is required');
      }

      return schema.nullable();
    }),
  accountNumber: yup.string().when('paymentMethod', (paymentMethod, schema) => {
    if (paymentMethod !== PAYMENT_METHODS.BANK) {
      return schema.nullable();
    }

    return schema.required('Account number is required').nullable();
  }),
  routingNumber: yup.string().when('paymentMethod', (paymentMethod, schema) => {
    if (paymentMethod !== PAYMENT_METHODS.BANK) {
      return schema.nullable();
    }

    return schema.required('Routing number is required').nullable();
  }),
  accountType: yup.string().when('paymentMethod', (paymentMethod, schema) => {
    if (paymentMethod !== PAYMENT_METHODS.BANK) {
      return schema.nullable();
    }

    return schema.required('Account type is required').nullable();
  }),
  email: yup.string().when(
    [
      'paymentType',
      'paymentMethod',
      // 'sendEmail',
      'emailReadOnly',
      'savePaymentTokenIsAvailable',
      'signUpUser',
      'emailOption'
    ],
    (
      paymentType,
      paymentMethod,
      // sendEmail,
      emailReadOnly,
      savePaymentTokenIsAvailable,
      signUpUser,
      emailOption,
      schema
    ) => {
      if (emailReadOnly) {
        return schema.required('Email is required').nullable();
      }

      if (paymentType === PAYMENT_TYPES.SUBSCRIPTION) {
        return schema
          .email('Email is not valid')
          .required('Email is required')
          .nullable();
      }

      if (paymentMethod === PAYMENT_TYPES.CRYPTO) {
        return schema
          .email('Email is not valid')
          .required('Email is required')
          .nullable();
      }

      if (emailOption === 'required') {
        return schema
          .required('Email is required')
          .email('Email is not valid')
          .nullable();
      }

      if (signUpUser) {
        return schema
          .required('Email is required')
          .email('Email is not valid')
          .nullable();
      }

      // trigger this condition only for card
      if (
        savePaymentTokenIsAvailable &&
        paymentMethod === PAYMENT_METHODS.CARD
      ) {
        return schema
          .required('Email is required')
          .email('Email is not valid')
          .nullable();
      }

      // if (paymentMethod === PAYMENT_METHODS.TERMINAL) {
      //   return schema.email('Email is not valid').nullable();
      // }

      // if (paymentMethod === PAYMENT_METHODS.BANK) {
      //   return schema.email('Email is not valid').nullable();
      // }

      // if (paymentMethod === PAYMENT_METHODS.CRYPTO) {
      //   return schema
      //     .required('Email is required')
      //     .email('Email is not valid')
      //     .nullable();
      // }

      // if (!sendEmail) {
      //   return schema.nullable();
      // }

      // if (paymentType === PAYMENT_TYPES.CHARGE) {
      //   return schema
      //     .required('Email is required')
      //     .email('Email is not valid')
      //     .nullable();
      // }

      return (
        schema
          // .required('Email is required')
          .email('Email is not valid')
          .nullable()
      );
    }
  ),
  selectedCryptoType: yup
    .object()
    .nullable()
    .test('is-wallet-selected', 'Please select a coin', (value, schema) => {
      if (schema.parent.paymentMethod !== PAYMENT_METHODS.CRYPTO) {
        return true;
      }

      if (!value) {
        return false;
      }

      if (!value.value) {
        return false;
      }

      return true;
    }),
  allowTip: yup.boolean().notRequired().nullable(),
  tipValue: yup.number().notRequired().nullable(),
  tipPercent: yup.number().notRequired().nullable(),
  sendEmail: yup.bool().notRequired().nullable(),
  signUpUser: yup.bool().notRequired().nullable(),
  savePaymentTokenIsAvailable: yup.bool().notRequired().nullable(),
  emailReadOnly: yup.bool().notRequired().nullable(),
  zipMode: yup.string().nullable(true).notRequired(),

  phoneOption: yup.string().notRequired().nullable(),
  phoneNumber: yup.string().when('phoneOption', (phoneOption, schema) => {
    if (!phoneOption || phoneOption === 'false') {
      return schema.notRequired().nullable();
    }

    if (phoneOption === 'optional') {
      return schema.notRequired().nullable();
    }

    return schema.required('Field is required').nullable(true);
  }),
  emailOption: yup.string().nullable(true).notRequired(),

  // ask for full name
  showFullName: yup.bool().notRequired().nullable(),
  // customer name
  fullNameDetails: yup.object().when('showFullName', (showFullName, schema) => {
    if (!showFullName) {
      return schema.notRequired().nullable();
    }

    return schema.shape({
      firstName: yup.string().required('Field is required').nullable(),
      lastName: yup.string().required('Field is required').nullable()
    });
  }),

  //billing

  // showBillingAddress: yup.bool().notRequired().nullable(),

  billingOptions: addressOptions,
  billingAddress: validateAddress('billingOptions'),

  // address
  addressOptions: addressOptions,
  address: validateAddress('addressOptions')
});

export default validationSchema;
