import { ref } from 'vue';
import { PAYMENT_TYPES, PAYMENT_TYPES_AS_ARRAY } from '@/config/constants.js';

export default function (data, paymentType) {
  const isBankPaymentAvailable = ref(true);

  if (!data.paymentType) {
    paymentType.value = PAYMENT_TYPES.CHARGE;
  } else if (PAYMENT_TYPES_AS_ARRAY.includes(paymentType.value)) {
    paymentType.value = data.paymentType;
  } else {
    console.error('Payment type is not valid');
    console.error(data.paymentType);
    console.warn(`Payment options: ${PAYMENT_TYPES_AS_ARRAY.join(', ')}`);
  }

  if (paymentType.value !== PAYMENT_TYPES.CHARGE) {
    isBankPaymentAvailable.value = false;
  }

  return {
    paymentType,
    isBankPaymentAvailable
  };
}
