import { computed, ref } from 'vue';
import { PAYMENT_TYPES } from '@/config/constants.js';

export default function ({
  parsed,
  paymentType,
  // signUpUser,
  // showBillingAddress,
  showFullName,
  phoneOption,
  emailReadOnly,
  emailOption
  // savePaymentTokenIsAvailable
}) {
  const optIn = ref(false);
  const showSaveCardOption = ref(false);
  const showDescription = ref(false);

  optIn.value = parsed.optIn;
  showSaveCardOption.value = parsed.savePaymentToken;
  emailOption.value = parsed.emailOption;

  // if (!optIn.value) {
  //   signUpUser.value = false;
  // }

  // if (!showSaveCardOption.value) {
  //   savePaymentTokenIsAvailable.value = false;
  // }

  // showBillingAddress.value = parsed.billingAddress;
  showFullName.value = parsed.fullName;
  phoneOption.value = parsed.phoneOption;
  showDescription.value =
    typeof parsed.showDescription === 'boolean' ? parsed.showDescription : true;

  const showEmailField = computed(() => {
    if (emailReadOnly.value) {
      return false;
    }

    if (paymentType.value === PAYMENT_TYPES.SUBSCRIPTION) {
      return true;
    }

    if (
      parsed.emailOption === 'required' ||
      parsed.emailOption === 'optional'
    ) {
      return true;
    }

    if (optIn.value || showSaveCardOption.value) {
      return true;
    }

    return false;
  });

  return {
    optIn,
    showEmailField,
    showSaveCardOption,
    showDescription
  };
}
