<template>
  <div
    class="base-input-container"
    :class="{
      error,
      ...inputClasses,
      showSuffix,
      showPrefix,
      isFocused,
      hasValue: modelValue
    }"
    :style="cssVariablesList"
  >
    <label class="w-100">
      <p v-if="label" class="label fw-normal" :class="`color-${labelColor}`">
        {{ label }}
      </p>

      <div class="d-flex align-items-center">
        <div
          v-if="showPrefix"
          class="prefix pre-input d-flex align-items-center justify-content-center px-2"
        >
          <slot name="prefix" />
        </div>

        <div class="w-100 input-parent">
          <span
            class="placeholder"
            :class="{ invisiblePlaceholder: modelValue }"
          >
            {{ placeholder }}
          </span>

          <input
            class="input w-100 input-state"
            :type="type"
            :placeholder="''"
            :value="modelValue"
            :maxlength="maxLength"
            :readonly="readonly"
            :step="step"
            :name="name"
            :autocomplete="autocomplete"
            @input="handleInput"
            @blur="handleBlur"
            @focus="handleFocus"
          />
        </div>

        <div
          v-if="showSuffix"
          class="suffix post-input d-flex align-items-center justify-content-center px-2"
        >
          <slot name="suffix" />
        </div>
      </div>
    </label>

    <transition name="slide-y">
      <p v-if="error" class="error-text mt-1 fw-bolder">
        {{ error }}
      </p>
    </transition>
  </div>
</template>

<script>
import { computed, ref, toRef } from '@vue/reactivity';
import {
  getCustomStyle,
  getCurrentStyle,
  getCustomClasses
} from '@/helpers/styles/index.js';
import AVAILABLE_TO_CONFIG_VALUES from '@/config/inputStyleValues.js';

/** BaseInput component instead of simple input
 *
 *  @example
 *  <base-input
 *   v-model="email"
 *   placeholder="Email"
 *   label="Email"
 *   type="text"
 *   error="Email is not valid"
 *  />
 *
 */

const DEFAULT_LABEL_COLOR = 'dark';

export default {
  name: 'BaseInput',
  props: {
    label: {
      type: String,
      default: null
    },
    readonly: {
      type: Boolean,
      default: null
    },
    labelColor: {
      type: String,
      default: DEFAULT_LABEL_COLOR
    },
    placeholder: {
      type: String,
      default: null
    },
    maxLength: {
      type: Number,
      default: null
    },
    type: {
      type: String,
      default: 'text'
    },
    step: {
      type: String,
      default: 'text'
    },
    modelValue: {
      type: String,
      default: null
    },
    error: {
      type: String,
      default: null
    },
    formatter: {
      type: Function,
      default: null
    },
    customStyle: {
      type: Object,
      default: null
    },
    name: {
      type: String,
      default: null
    },
    autocomplete: {
      type: String,
      default: null
    }
  },
  emits: ['update:modelValue', 'blur', 'focus'],
  setup(props, { emit, slots }) {
    const isFocused = ref(false);
    const formatter = toRef(props, 'formatter');
    const customStyle = toRef(props, 'customStyle');
    const showSuffix = slots.suffix;
    const showPrefix = slots.prefix;

    const format = (value, target) => {
      if (typeof formatter.value !== 'function') {
        return value;
      }

      const formatted = formatter.value(value);
      target.value = formatted;

      return formatted;
    };
    const handleInput = (e) => {
      emit('update:modelValue', format(e.target.value, e.target));
    };

    const handleFocus = () => {
      isFocused.value = true;

      emit('focus');
    };

    const handleBlur = () => {
      isFocused.value = false;

      emit('blur');
    };

    const currentStyle = computed(() => {
      return getCurrentStyle(customStyle.value);
    });

    const cssVariablesList = computed(() => {
      const style = getCustomStyle(
        currentStyle.value,
        AVAILABLE_TO_CONFIG_VALUES
      );

      return style;
    });

    const inputClasses = computed(() => {
      return getCustomClasses(currentStyle.value, AVAILABLE_TO_CONFIG_VALUES);
    });

    return {
      handleInput,
      handleBlur,
      handleFocus,
      inputClasses,
      cssVariablesList,
      showSuffix,
      showPrefix,
      isFocused
    };
  }
};
</script>

