import { computed } from 'vue';
import {
  getCustomStyle,
  getCurrentStyle,
  getCustomClasses
} from '@/helpers/styles/index.js';

const AVAILABLE_TO_CONFIG_VALUES = [
  {
    name: 'bodyError',
    extend: ['color', 'font-weight', 'font-size']
  }

  //   {
  //     name: 'color'
  //   },
  //   {
  //     name: 'font-size'
  //   },
  //   {
  //     name: 'fontWeight'
  //   }
];

export default function (errorStyle) {
  const currentErrorStyle = computed(() => {
    return getCurrentStyle(errorStyle);
  });

  const errorCssVariablesList = computed(() => {
    const style = getCustomStyle(
      currentErrorStyle.value,
      AVAILABLE_TO_CONFIG_VALUES
    );

    return style;
  });

  const errorClasses = computed(() => {
    return getCustomClasses(
      currentErrorStyle.value,
      AVAILABLE_TO_CONFIG_VALUES
    );
  });

  return {
    errorCssVariablesList,
    errorClasses
  };
}
