<template>
  <div
    aria-live="polite"
    aria-atomic="true"
    class="d-flex justify-content-center align-items-center w-100 position-fixed pt-2 toast-container"
    data-test="toast"
  >
    <transition name="slide-y">
      <div
        v-if="showToast"
        class="toast d-block"
        role="alert"
        aria-live="assertive"
        aria-atomic="true"
        data-test="content"
      >
        <div class="d-flex">
          <div class="status-color" :class="toastType" data-test="color"></div>
          <div class="main-content">
            <div class="toast-header">
              <strong class="me-auto"> {{ toastOptions.title }} </strong>
              <button
                v-if="toastOptions.canClose"
                type="button"
                class="btn-close"
                data-test="close"
                @click="handleCloseClick"
              ></button>
            </div>
            <div class="toast-body">
              {{ toastOptions.text }}
            </div>
          </div>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
import { computed, ref } from '@vue/reactivity';
import { useStore } from 'vuex';
import { watch } from '@vue/runtime-core';

/** Component for showing message notifications
 *
 *  @example
 *  import useToast from '@/composables/common/toast';
 *  const { showMessage } = useToast();
 *
 *  showMessage({
 *    title: 'Test title',
 *    text: 'Hello world',
 *    delay: 5000,  => default 2000
 *    type: 'success', => default 'warning', any from /variables.scss => $colors
 *    autohide: true, => hide with delay or don't
 *    canClose: true => show close button or don't
 *  })
 *
 *  @remark
 *  Please do not import this component directly.
 *  Use composable instead*
 *
 */

export default {
  name: 'BaseToast',
  setup() {
    const store = useStore();
    const timerId = ref(null);

    const toastOptions = computed(() => {
      return store.state.toast.options;
    });

    const showToast = computed(() => {
      return store.state.toast.showToast;
    });

    const toastType = computed(() => {
      return `bg-${toastOptions.value?.type}`;
    });

    const closeToast = () => {
      store.commit('toast/close');
      clearTimeout(timerId.value);
    };

    watch(
      () => showToast.value,
      (newValue) => {
        if (!newValue) {
          return;
        }

        if (!toastOptions.value.autohide) {
          return;
        }

        timerId.value = setTimeout(() => {
          closeToast();
        }, toastOptions.value.delay);
      }
    );

    const handleCloseClick = () => {
      closeToast();
    };

    return {
      showToast,
      toastOptions,
      handleCloseClick,
      toastType
    };
  }
};
</script>

<style lang="scss" scoped>
.toast-header {
  border-bottom: none;
}

.toast-container {
  z-index: 999999999;
}

.toast {
  @include has-box-shadow;
  border: none;
}

.toast-header {
  strong {
    color: var(--dark);
  }
}

.toast-body {
  color: var(--c-grey-medium);
  background-color: rgba(255, 255, 255, 0.85);
}

.status-color {
  width: 20px;
  border-top-left-radius: 0.25rem;
  border-bottom-left-radius: 0.25rem;
}

.main-content {
  flex-grow: 1;
}

.btn-close {
  opacity: 0.2;
}
</style>
