const wait = (amount) => {
  return new Promise((resolve) => {
    setTimeout(() => {
      resolve();
    }, amount);
  });
};

class ApiService {
  constructor() {
    this.classes = [];
    this.services = [];
  }

  setServices(serviceClasses) {
    this.classes = [...serviceClasses];
  }

  init(params) {
    this.classes.forEach((ServiceClass) => {
      const service = new ServiceClass(params);

      const name = service.name;

      const exists = this.services.find((s) => s.name === name);

      if (exists) {
        return;
      }

      service.init();

      this.services.push(service);
    });
  }

  getAvailable() {
    const service = this.services.find((s) => {
      return s.available;
    });

    if (!service) {
      throw new Error('No service available');
    }

    return service;
  }

  post(url, data) {
    const service = this.getAvailable();

    return service.post(url, data);
  }

  async get(url, data) {
    await wait(800);
    const service = this.getAvailable();

    return service.get(url, data);
  }

  findService(serviceName) {
    return this.services.find((s) => s.name === serviceName);
  }
}

const apiService = new ApiService();

export default apiService;
