import { ref } from 'vue';

import messanger from '@/services/messages';
import { debounce } from '@/helpers';

const ADDITIONAL_SPACE_VALUE = 10;
const RESIZE_REPEAT_MS = 20;

export default function ({ newWindow }) {
  const resizeWindowOnUpdate = ref(true);

  const getFormElementHeight = () => {
    const formContainer = document.querySelector('#form_container');

    return (formContainer && formContainer.offsetHeight) || 0;
  };

  const updateIframeHeight = () => {
    // no need to update iframe height if we're using separate window
    if (newWindow.value) {
      return;
    }

    // update iframe height depending on content height
    messanger.send(
      'change-iframe-size',
      getFormElementHeight() + ADDITIONAL_SPACE_VALUE
    );
  };

  const triggerIframeHeightResize = () => {
    if (!resizeWindowOnUpdate.value) {
      return;
    }

    updateIframeHeight();
  };

  const debouncedTrigger = debounce(
    triggerIframeHeightResize,
    RESIZE_REPEAT_MS
  );

  const getResizeOnUpdateValue = (parsed) => {
    const v = typeof parsed.resize === 'boolean' ? parsed.resize : true;
    resizeWindowOnUpdate.value = v;
  };

  const triggerDocumentClass = (parsed) => {
    if (!document.body) {
      return;
    }

    if (resizeWindowOnUpdate.value && !parsed.newWindow) {
      document.body.classList.add('resizable');
    }
  };

  const resizeObserver = new ResizeObserver(() => {
    debouncedTrigger();
  });

  // start observing a DOM node
  resizeObserver.observe(document.body);

  return {
    resizeWindowOnUpdate,

    updateIframeHeight,
    triggerIframeHeightResize,
    getResizeOnUpdateValue,
    triggerDocumentClass
  };
}
