import useService from '@/composables/common/services';
import { ref } from '@vue/reactivity';

export default function () {
  const { apiService } = useService();
  const cryptoCurrenciesList = ref([]);
  const loadingCryptoOptions = ref(false);

  const fetchCryptoCurrencies = () => {
    loadingCryptoOptions.value = true;

    apiService
      .get('charge/available')
      .then((res) => {
        let options = [];
        if (Array.isArray(res)) {
          options = res;
        } else {
          options = [];
        }

        loadingCryptoOptions.value = false;
        cryptoCurrenciesList.value = options.map((el) => {
          const logo = `${
            process.env.VUE_APP_SRC_BASE_URL
          }/static/images/coins/32/color/${el.currencycode.toLowerCase()}.png`;

          return {
            value: el.currencycode,
            name: el.currencycode,
            fullname: el.currencyname,
            logo
          };
        });

        // cryptoCurrenciesList.value.push({
        //   value: 'test',
        //   name: 'TST',
        //   fullname: 'test'
        // });
      })
      .catch(() => {
        loadingCryptoOptions.value = false;
      });
  };

  return {
    fetchCryptoCurrencies,
    cryptoCurrenciesList,
    loadingCryptoOptions
  };
}
