<template>
  <div>
    <div v-if="paymentStatus !== 'error'">
      <p class="text-center color-dark">To finish your payment</p>
      <p class="text-center color-dark">
        of
        <span class="fw-600"
          >${{ Number(amount).toFixed(2) }} ({{ data.cryptocode }}
          {{ data.expectedamount }})</span
        >
      </p>
      <!-- <p class="text-center color-dark crypto-price">
      <span class="fw-600">
        {{ data.htmlsymbol }}{{ data.expectedamount }}</span
      >
    </p> -->

      <p class="text-center color-dark fz-18 fw-500 mt-3">Scan QR</p>

      <div class="qr-code d-flex justify-content-center">
        <div class="img-container">
          <img v-if="data.qr" :src="data.qr" alt="QR code" />
        </div>
        <!-- <canvas ref="qr"></canvas> -->
      </div>

      <p class="text-center color-dark fz-14 fw-500">OR</p>

      <p class="text-center color-dark fz-14 mt-2">Copy address below</p>
      <base-input
        :custom-style="customStyle"
        readonly
        class="mt-1"
        :model-value="data.address"
      >
        <template #suffix>
          <span role="button" @click="handleCopy"> <content-copy /> </span>
        </template>
      </base-input>
    </div>

    <transition name="slide-y">
      <div
        v-if="paymentStatus === 'success'"
        class="d-flex justify-content-center pt-4"
      >
        <div class="check">
          <Check />
        </div>
      </div>
    </transition>

    <transition name="slide-y">
      <div
        v-if="paymentStatus === 'error'"
        class="d-flex justify-content-center"
      >
        <div class="error-el">
          <Close />
        </div>
      </div>
    </transition>

    <transition name="slide-y">
      <div
        v-if="paymentStatus === 'incomplete'"
        class="d-flex justify-content-center pt-4"
      >
        <div class="alert-el">
          <alert-circle />
        </div>
      </div>
    </transition>

    <div
      class="crypto-payment-status position-relative d-flex align-items-center fw-400 mt-3"
      :class="{
        [paymentStatus]: paymentStatus,
        'justify-content-between': paymentStatus === 'pending',
        'justify-content-center': paymentStatus !== 'pending'
      }"
    >
      <span v-if="paymentStatus === 'pending'" class="text-detail"></span>
      <div
        v-if="paymentStatus === 'pending'"
        class="spinner-border text-primary"
        role="status"
      >
        <span class="visually-hidden">Loading...</span>
      </div>

      <span>
        {{ message }}
      </span>

      <span
        v-if="paymentStatus === 'pending'"
        class="text-center fz-16 color-primary fw-500 text-detail"
      >
        {{ formatValue(timer.minutes) }}:{{ formatValue(timer.seconds) }}
      </span>
    </div>

    <transition name="slide-y">
      <div
        v-if="paymentStatus === 'error' && data.canceledreason"
        class="text-center pt-2"
      >
        <span class="color-danger">
          {{ data.canceledreason }}
        </span>
      </div>
    </transition>

    <transition name="slide-y">
      <div v-if="paymentStatus === 'incomplete'" class="pt-2">
        <p class="text-center fz-14 color-dark">
          Received:
          <span class="fw-500 fz-16"
            >{{ data.cryptocode }} {{ data.netamount }}</span
          >
        </p>

        <p class="text-center fz-14 color-dark">
          Amount due:
          <span class="fw-500 color-danger fz-16"
            >{{ data.cryptocode }} {{ Math.abs(data.overunderamount) }}</span
          >
        </p>
      </div>
    </transition>

    <transition name="slide-y">
      <div v-if="paymentStatus === 'success'" class="pt-2">
        <p class="text-center fz-14 color-dark">
          Received:
          <span class="fw-500 fz-16"
            >{{ data.cryptocode }} {{ data.netamount }}</span
          >
        </p>
      </div>
    </transition>

    <transition name="slide-y">
      <div
        v-if="paymentStatus === 'incomplete' || paymentStatus === 'success'"
        class="fz-14 color-dark pt-2"
      >
        <p class="fw-500 text-center color-dark">TX Hash</p>

        <p class="text-center color-dark mt-0 txhash">
          {{ data.txhash }}
        </p>
      </div>
    </transition>

    <transition name="slide-y">
      <p
        v-if="paymentStatus === 'incomplete'"
        class="text-center fz-14 fw-400 color-primary pt-4"
      >
        Order will be canceled if full payment is not received
      </p>
    </transition>

    <!-- <div>
      <p class="text-center fz-16 color-dark fw-500 mt-2">
        {{ formatValue(timer.minutes) }}:{{ formatValue(timer.seconds) }}
      </p>
    </div> -->
  </div>
</template>

<script>
// import QRious from 'qrious';
import { computed, toRefs } from '@vue/runtime-core';
import useCopy from '@/composables/common/copy/index.js';

import ContentCopy from 'vue-material-design-icons/ContentCopy.vue';
import Check from 'vue-material-design-icons/Check.vue';
import Close from 'vue-material-design-icons/Close.vue';
import AlertCircle from 'vue-material-design-icons/AlertCircle.vue';
import BaseInput from '@/components/base/BaseInput.vue';

const MESSAGES = {
  pending: 'Waiting for payment',
  success: 'Transaction successful',
  error: 'Payment was cancelled',
  incomplete: 'Transaction incomplete'
};

export default {
  name: 'CryptoQr',
  components: {
    BaseInput,
    ContentCopy,
    Check,
    Close,
    AlertCircle
  },
  props: {
    amount: {
      type: [String, Number],
      default: 0
    },
    customStyle: {
      type: Object,
      default: null
    },
    qr: {
      type: String,
      default: null
    },
    data: {
      type: Object,
      default: () => {}
    },
    timer: {
      type: Object,
      default: () => {}
    }
  },
  setup(props) {
    const { data } = toRefs(props);
    const { copy } = useCopy();

    const paymentStatus = computed(() => {
      return data.value.status;
    });

    const handleCopy = () => {
      copy(data.value.address);
    };

    const message = computed(() => {
      return MESSAGES[paymentStatus.value];
    });

    const formatValue = (value) => {
      if (value < 10) {
        return `0${value}`;
      }

      return value;
    };

    return {
      // qr,
      paymentStatus,
      message,
      handleCopy,
      formatValue
    };
  }
};
</script>

