<template>
  <label
    class="radio p-3 position-relative d-flex align-items-center"
    :class="{ checked: modelValue === value, ...radioClasses }"
    :style="cssVariablesList"
  >
    <input
      type="radio"
      class="radio-button mr-5"
      :value="value"
      :checked="modelValue === value"
      @input="$emit('update:modelValue', value)"
    />
    <div class="pl-1 position-relative w-100 pl-2">
      {{ label }}

      <slot name="default"></slot>
    </div>
  </label>
</template>

<script>
import {
  getCustomStyle,
  getCurrentStyle,
  getCustomClasses
} from '@/helpers/styles/index.js';
import { computed, toRef } from '@vue/reactivity';

const AVAILABLE_TO_CONFIG_VALUES = [
  {
    name: 'color'
  },
  {
    name: 'border-radius'
  },
  {
    name: 'border-color'
  },
  {
    name: 'border-width'
  },
  {
    name: 'padding'
  },
  {
    name: 'padding-top'
  },
  {
    name: 'padding-left'
  },
  {
    name: 'padding-right'
  },
  {
    name: 'padding-bottom'
  },
  {
    name: 'font-size'
  },
  {
    name: 'font-weight'
  },
  {
    name: 'background'
  },
  {
    name: 'border-left-style'
  },
  {
    name: 'border-right-style'
  },
  {
    name: 'border-top-style'
  },
  {
    name: 'checked',
    extend: ['color', 'font-weight', 'font-size', 'background', 'border-color']
  }
];

export default {
  name: 'BaseRadio',
  props: {
    modelValue: {
      type: [String, Number],
      default: null
    },
    label: {
      type: String,
      required: true
    },
    value: {
      type: [String, Number],
      required: true
    },
    customStyle: {
      type: Object,
      default: () => {}
    }
  },
  emits: ['update:modelValue'],
  setup(props) {
    const customStyle = toRef(props, 'customStyle');

    const cssVariablesList = computed(() => {
      const currentStyle = getCurrentStyle(customStyle.value);
      const style = getCustomStyle(currentStyle, AVAILABLE_TO_CONFIG_VALUES);

      return {
        ...style
      };
    });

    const radioClasses = computed(() => {
      const currentStyle = getCurrentStyle(customStyle.value);

      return getCustomClasses(currentStyle, AVAILABLE_TO_CONFIG_VALUES);
    });

    return {
      cssVariablesList,
      radioClasses
    };
  }
};
</script>

<style lang="scss" scoped>
