  font-weight: 500;
  border-radius: 10px;
  color: var(--primary);
  border: 1px solid var(--c-grey-light);
  background: #f3f3f3;
  color: #939393;
  transition: all 0.2s;
  position: relative;

  &:not(.checked) {
    cursor: pointer;
  }
}

.radio-button {
  left: 0;
  top: 0;
  height: 0;
  width: 0;
  background: transparent;
  border: none;
  appearance: none !important;

  &::before {
    width: 14px;
    height: 14px;
    border: 2px solid #939393;
    display: block;
    // top: 50%;
    transform: translateY(-50%);
    content: '';
    position: absolute;
    border-radius: 50%;
    left: 13px;
    transition: all 0.2s;
  }

  &:after {
    width: 0;
    height: 0;
    display: block;
    background: var(--primary);
    left: 17px;
    // top: 50%;
    transform: translateY(-50%);
    content: '';
    position: absolute;
    border-radius: 50%;
    transition: all 0.2s;
    transform-origin: center;
    transform-origin: center;
  }
}

.checked {
  background: #fffbf5;
  color: var(--primary);
  border-color: var(--primary);

  .radio-button {
    &::before {
      border-color: var(--primary);
    }

    &:after {
      width: 6px;
      height: 6px;
    }
  }
}

.has-custom-color {
  color: var(--custom-color);

  .radio-button {
    &::before {
      border-color: var(--custom-color);
    }

    &:after {
      background: var(--custom-color);
    }
  }
}

.has-custom-border-radius {
  border-radius: var(--custom-border-radius);
}

.has-custom-border-color {
  border-color: var(--custom-border-color);
}

.has-custom-font-size {
  font-size: var(--custom-font-size);
}

.has-custom-font-weight {
  font-weight: var(--custom-font-weight);
}

.has-custom-background {
  background: var(--custom-background);
}

.has-custom-checked-color.checked {
  color: var(--custom-checked-color);

  .radio-button {
    &::before {
      border-color: var(--custom-checked-color);
    }

    &:after {
      background: var(--custom-checked-color);
    }
  }
}

.has-custom-checked-font-weight.checked {
  font-weight: var(--custom-checked-font-weight);
}

.has-custom-checked-background.checked {
  background: var(--custom-checked-background);
}

.has-custom-checked-border-color.checked {
  border-color: var(--custom-checked-border-color);
}

.has-custom-border-width {
  border-width: var(--custom-border-width) !important;
}

.has-custom-border-left-style {
  border-left-style: var(--custom-border-left-style);
}

.has-custom-border-right-style {
  border-right-style: var(--custom-border-right-style);
}

.has-custom-border-top-style {
  border-top-style: var(--custom-border-top-style);
}

.has-custom-padding {
  padding: var(--custom-padding) !important;
}

.has-custom-padding-top {
  padding-top: var(--custom-padding-top) !important;
}

.has-custom-padding-left {
  padding-left: var(--custom-padding-left) !important;
}

.has-custom-padding-right {
  padding-right: var(--custom-padding-right) !important;
}
.has-custom-padding-bottom {
  padding-bottom: var(--custom-padding-bottom) !important;
}
</style>
