import { ref } from 'vue';
import useService from '@/composables/common/services';

export default function (email) {
  const { apiService } = useService();

  const tokensList = ref([]);
  const tokensListLoading = ref(false);

  const getAvailableTokens = () => {
    if (!email.value) {
      return;
    }

    const http = apiService.findService('http');

    tokensListLoading.value = true;

    // 420475320    211374020
    http
      .get(`wallet?id=${email.value}`)
      .then((res) => {
        tokensListLoading.value = false;
        tokensList.value = res.message.wallet.map((el) => {
          const [_, details] = el.split('-');
          const [card, number] = details.split(':');
          const type = (card || '').toLowerCase() === 'bank' ? 'bank' : 'card';
          let walletNumber;

          if (type === 'bank') {
            walletNumber = number.replace('*', '***** ');
          } else {
            walletNumber = number.replace('*', '**** **** **** ');
          }

          return {
            card: card.toLowerCase(),
            walletNumber,
            token: el,
            type
          };
        });
      })
      .catch(() => {
        tokensListLoading.value = false;
      });
  };

  return {
    tokensList,
    tokensListLoading,

    getAvailableTokens
  };
}
