<script>
import {
  getCustomStyle,
  getCurrentStyle,
  getCustomClasses
} from '@/helpers/styles/index.js';
import { computed, ref, toRef } from '@vue/reactivity';
import BaseSpinner from '@/components/base/BaseSpinner.vue';
import BaseInput from '@/components/base/BaseInput.vue';

const AVAILABLE_TO_CONFIG_VALUES = [
  {
    name: 'color',
    states: [{ name: 'active' }, { name: 'hover' }, { name: 'checked' }]
  },
  {
    name: 'border-color',
    states: [{ name: 'active' }, { name: 'hover' }, { name: 'checked' }]
  },
  {
    name: 'border-radius'
  },
  {
    name: 'font-size'
  },
  {
    name: 'font-weight'
  },
  {
    name: 'background',
    states: [{ name: 'active' }, { name: 'hover' }, { name: 'checked' }]
  }
];

// 'USDC'
const TOP_OPTIONS_LIST = ['BTC', 'ETH', 'BCH', 'USDC'];

export default {
  name: 'CryptoPayment',
  components: {
    BaseInput,
    BaseSpinner
  },
  props: {
    modelValue: {
      type: Object,
      default: null
    },
    customStyle: {
      type: Object,
      default: null
    },
    cryptoOptions: {
      type: Array,
      default: () => []
    },
    loading: {
      type: Boolean,
      default: false
    },
    inputStyle: {
      type: Object,
      default: () => {}
    }
  },
  emits: ['update:modelValue'],
  setup(props, { emit }) {
    const customStyle = toRef(props, 'customStyle');
    const cryptoOptions = toRef(props, 'cryptoOptions');
    const search = ref('');
    const showMoreOptions = ref(false);
    const showMoreOptionsTransitionFinished = ref(true);

    const handleOptionClick = (option) => {
      emit('update:modelValue', option);
    };

    const handleMoreOptionsClick = () => {
      if (!showMoreOptionsTransitionFinished.value) {
        return;
      }

      showMoreOptionsTransitionFinished.value = false;
      showMoreOptions.value = !showMoreOptions.value;
      setTimeout(() => {
        showMoreOptionsTransitionFinished.value = true;
      }, 300);
    };

    const checkStyle = computed(() => {
      const currentStyle = getCurrentStyle(customStyle.value);
      const style = getCustomStyle(currentStyle, AVAILABLE_TO_CONFIG_VALUES);

      return {
        ...style
      };
    });

    const checkClasses = computed(() => {
      const currentStyle = getCurrentStyle(customStyle.value);

      return getCustomClasses(currentStyle, AVAILABLE_TO_CONFIG_VALUES);
    });

    const topOptions = computed(() => {
      return cryptoOptions.value.filter((v) => {
        return TOP_OPTIONS_LIST.includes(v.value);
      });
    });

    const otherOptions = computed(() => {
      return cryptoOptions.value.filter((v) => {
        return !TOP_OPTIONS_LIST.includes(v.value);
      });
    });

    const filteredOptions = computed(() => {
      return otherOptions.value.filter((el) => {
        const name = (el.name || '').toLowerCase();
        const fullname = (el.fullname || '').toLowerCase();

        return (
          name.includes(search.value.toLowerCase()) ||
          fullname.includes(search.value.toLowerCase())
        );
      });
    });

    return {
      showMoreOptions,
      topOptions,
      otherOptions,
      showMoreOptionsTransitionFinished,
      search,
      filteredOptions,

      handleOptionClick,
      handleMoreOptionsClick,
      checkStyle,
      checkClasses
    };
  }
};
</script>

