import { ref } from 'vue';

const DEFAULT_TEXT = 'View our terms and conditions';

export default function (parsed) {
  const terms = ref(null);

  const getTermsValue = (v) => {
    if (!v) {
      return null;
    }

    if (typeof v === 'string') {
      return {
        text: 'View our terms and conditions',
        url: v
      };
    }

    return {
      text: v.text || DEFAULT_TEXT,
      url: v.url
    };
  };

  terms.value = getTermsValue(parsed.terms);

  return {
    terms
  };
}
