<template>
  <button
    class="base-button"
    :type="type"
    :class="{
      hasIconOnly,
      rounded,
      [`bg-${color}`]: !!color,
      [`bg-${color}--hover`]: !!color,
      [`bg-${color}--active`]: !!color,
      ...btnClasses
    }"
    :style="btnStyle"
  >
    <slot name="default" />

    <span v-if="hasIconOnly" class="icon-container d-inline-block">
      <slot name="icon-only" />
    </span>
  </button>
</template>

<script>
import { computed, toRef } from '@vue/reactivity';
const DEFAULT_SIZE = 50;
const DEFAULT_COLOR = 'primary';
import {
  getCustomStyle,
  getCurrentStyle,
  getCustomClasses
} from '@/helpers/styles/index.js';

/** Button component instead of simple button
 *
 *  @example
 *  <base-button
 *   @click="handleClick"
 *   type="submit"
 *   color="danger"
 *  >
 *   Click me!
 *  </base-button>
 *
 *  <base-button
 *   @click="handleClick"
 *   type="submit"
 *   color="danger"
 *   size="55px"
 *   rounded
 *  >
 *   <template #icon-only>
 *      <img src="..icon" alt="icon" />
 *   </template>
 *  </base-button>
 *
 *  @remark
 *  Please note, property "size" is applied
 *  only if "rounded" exists
 *
 */

const AVAILABLE_TO_CONFIG_VALUES = [
  {
    name: 'color',
    states: [{ name: 'active' }, { name: 'hover' }]
  },
  {
    name: 'border-radius'
  },
  {
    name: 'height'
  },
  {
    name: 'font-size'
  },
  {
    name: 'font-weight'
  },
  {
    name: 'background',
    states: [{ name: 'active' }, { name: 'hover' }]
  }
];

export default {
  name: 'BaseButton',
  props: {
    //html type of button
    type: {
      type: String,
      default: null
    },
    //rounded button or not
    rounded: {
      type: Boolean,
      default: false
    },
    //height/width of rounded button
    size: {
      type: Number,
      default: DEFAULT_SIZE
    },
    //any color taken from variables.scss
    color: {
      type: String,
      default: DEFAULT_COLOR
    },
    customStyle: {
      type: Object,
      default: null
    }
  },
  setup(props, { slots }) {
    const hasIconOnly = slots['icon-only'];
    const customStyle = toRef(props, 'customStyle');

    const btnStyle = computed(() => {
      const currentStyle = getCurrentStyle(customStyle.value);
      const style = getCustomStyle(currentStyle, AVAILABLE_TO_CONFIG_VALUES);

      if (!props.rounded) {
        return style;
      }

      return {
        width: `${props.size}px`,
        height: `${props.size}px`,
        padding: '0px',
        ...style
      };
    });

    const btnClasses = computed(() => {
      const currentStyle = getCurrentStyle(customStyle.value);

      return getCustomClasses(currentStyle, AVAILABLE_TO_CONFIG_VALUES);
    });

    return {
      hasIconOnly,
      btnStyle,
      btnClasses
    };
  }
};
</script>

<style lang="scss" scoped>
.base-button {
  @include has-border-radius;
  @include transition(background-color);
  height: 50px;
  font-weight: 400;
  border: none;
  outline: none;
  color: #fff;
}

.icon-container {
  width: 30px;
}

.rounded {
  border-radius: 50% !important;
}

::v-deep(.icon-container) {
  img {
    width: 100%;
  }
}

.has-custom-color {
  color: var(--custom-color) !important;
}

.has-custom-height {
  height: var(--custom-height) !important;
}

.has-custom-hover-color {
  &:hover {
    color: var(--custom-color) !important;
    color: var(--custom-hover-color) !important;
  }
}

.has-custom-active-color {
  &:active {
    color: var(--custom-color) !important;
    color: var(--custom-active-color) !important;
  }
}

.has-custom-background {
  background: var(--custom-background) !important;
}

.has-custom-hover-background {
  &:hover {
    background: var(--custom-hover-background) !important;
  }
}

.has-custom-active-background {
  &:active {
    background: var(--custom-active-background) !important;
  }
}

.has-custom-border-radius {
  border-radius: var(--custom-border-radius) !important;
}

.has-custom-font-size {
  font-size: var(--custom-font-size) !important;
}

.has-custom-font-weight {
  font-weight: var(--custom-font-weight) !important;
}
</style>
