import useService from '@/composables/common/services';
import { ref } from 'vue';
import { LightenDarkenColor } from '@/helpers';
import useStyles from '@/composables/payment/styles.js';

const TRANSITION_TIME = 200;

export default function ({ parsed }) {
  const {
    buttonStyle,
    inputStyle,
    radioStyle,
    cryptoCheckboxStyle,
    errorStyle
  } = useStyles(parsed);

  const { apiService } = useService();
  const clientDetails = ref({});
  const colorsData = ref({});
  const colorsDataIsLoading = ref(false);

  const attachStyles = () => {
    const isActive = colorsData.value?.button?.isActive;

    if (!isActive) {
      return;
    }

    const colorData = colorsData.value.button.color;

    const color = colorData.value;
    const lighter = LightenDarkenColor(color.slice(1, color.length), 20);
    const lightest = LightenDarkenColor(color.slice(1, color.length), 40);

    if (!Object.keys(buttonStyle.value).length) {
      buttonStyle.value = {
        background: colorData.value,
        color: '#fff',
        hover: {
          background: `#${lighter}`
        },
        active: {
          background: `#${lightest}`
        }
      };
    }

    if (!Object.keys(inputStyle.value).length) {
      inputStyle.value = {
        focus: {
          borderColor: colorData.value
        }
      };
    }

    if (!Object.keys(radioStyle.value).length) {
      radioStyle.value = {
        checked: {
          color: colorData.value,
          borderColor: colorData.value,
          background: lightest
        }
      };
    }
  };

  const fetchClientDetails = () => {
    const http = apiService.findService('http');

    colorsDataIsLoading.value = true;
    return http
      .get('client', {
        headers: {
          Authorization: `bearer ${parsed.apikey}`
        }
      })
      .then((res) => {
        clientDetails.value = res;
        colorsData.value =
          res?.message?.config?.whitelabel?.appearance?.colors || {};

        attachStyles();

        setTimeout(() => {
          colorsDataIsLoading.value = false;
        }, TRANSITION_TIME);
      })
      .catch(() => {
        colorsDataIsLoading.value = false;
      });
  };

  return {
    fetchClientDetails,

    clientDetails,
    colorsData,
    colorsDataIsLoading,

    buttonStyle,
    inputStyle,
    radioStyle,
    cryptoCheckboxStyle,
    errorStyle
  };
}
