import { ref } from 'vue';

export default function (data) {
  const { styles = {} } = data;

  const buttonStyle = ref(styles.button || {});
  const inputStyle = ref(styles.input || {});
  const radioStyle = ref(styles.radio || {});
  const cryptoCheckboxStyle = ref(styles.cryptoCheck || {});
  const errorStyle = ref(styles.error || {});

  return {
    buttonStyle,
    inputStyle,
    radioStyle,
    cryptoCheckboxStyle,
    errorStyle
  };
}
