export default function ({ amount, tipValue, tipPercent }) {
  const getOnePercent = () => amount.value / 100;

  const setDefaultPercent = (percent) => {
    const onePercent = getOnePercent();
    const tipNumber = +(percent * onePercent).toFixed(2);

    tipPercent.value = percent;
    tipValue.value = tipNumber.toFixed(2);
  };

  const handlePercentTipClick = (percent, e) => {
    e.preventDefault();
    const onePercent = getOnePercent();

    const tip = (percent * onePercent).toFixed(2);

    tipValue.value = tip;
    tipPercent.value = percent;
  };

  const handleCustomTipInput = () => {
    const onePercent = getOnePercent();
    const tipPercentValue = (tipValue.value / onePercent).toFixed(0);

    tipPercent.value = Number(tipPercentValue);
  };

  return {
    handlePercentTipClick,
    handleCustomTipInput,
    setDefaultPercent
  };
}
