import {
  formatCreditCardNumber,
  getValueWithSlash,
  removeNotDigitCharacters
} from '@/helpers';

export default function (date) {
  const formatCardNumber = (v) => {
    return formatCreditCardNumber(v);
  };

  const formatDate = (v) => {
    const value = getValueWithSlash(v, date.value);

    return value;
  };

  const onlyDigit = (v) => {
    return removeNotDigitCharacters(v);
  };

  return {
    formatCardNumber,
    formatDate,
    onlyDigit
  };
}
