import httpService from '@/services/http';

class HttpService {
  constructor({ token }) {
    this.service = httpService;
    this.token = token;
    this.available = true;
    this.name = 'http';
  }

  init() {
    return new Promise((resolve) => {
      this.service.interceptors.request.use(
        (config) => {
          config.headers = {
            ...config.headers,
            Authorization: `bearer ${this.token}`
          };

          return config;
        },
        function (error) {
          // Do something with request error
          return Promise.reject(error);
        }
      );

      resolve();
    });
  }

  get(url, data) {
    return new Promise((resolve, reject) => {
      return this.service
        .get(`/${url}`, data)
        .then((res) => {
          const data = res.data;

          if (typeof data.status === 'boolean' && !data.status) {
            return reject(data);
          }

          resolve(data);

          // if (data.status) {
          //   resolve(data);
          // } else {
          //   reject(data);
          // }
        })
        .catch((err) => reject(err));
    });
  }

  post(url, data) {
    return new Promise((resolve, reject) => {
      return this.service
        .post(`/${url}`, data)
        .then((res) => {
          const data = res.data;

          if (typeof data.status === 'boolean' && !data.status) {
            return reject(data);
          }

          resolve(data);

          // if (data.status) {
          //   resolve(data);
          // } else {
          //   reject(data);
          // }
        })
        .catch((err) => reject(err));
    });
  }
}

export default HttpService;
