import { useForm, useField } from 'vee-validate';
import {
  PAYMENT_METHODS,
  PAYMENT_TYPES,
  PAYMENT_OPTIONS,
  CARD_TOKEN_MODES
} from '@/config/constants.js';
import validationSchema from '@/config/fieldsSchema.js';

const DEFAULT_COUNTRY = 'US';

export default function () {
  const { errors, validate, setErrors } = useForm({
    validationSchema: validationSchema
  });

  // Crypto currency

  const { value: selectedCryptoType, errorMessage: selectedCryptoTypeError } =
    useField('selectedCryptoType', null, {
      initialValue: null
    });

  // Payment options

  const { value: paymentOptions } = useField('paymentOptions', null, {
    initialValue: [...PAYMENT_OPTIONS]
  });

  // Payment type

  const { value: paymentType } = useField('paymentType', null, {
    initialValue: PAYMENT_TYPES.CHARGE
  });

  // Payment method

  const { value: paymentMethod } = useField('paymentMethod', null, {
    initialValue: PAYMENT_METHODS.CARD
  });

  // Credit card fields

  const {
    value: cardNumber,
    errorMessage: cardNumberError,
    validate: validateCardNumber
  } = useField('cardNumber', null, { validateOnValueUpdate: false });
  const {
    value: date,
    errorMessage: dateError,
    validate: validateDate
  } = useField('date', null, {
    validateOnValueUpdate: false
  });
  const {
    value: cvc,
    errorMessage: cvcError,
    validate: validateCvc
  } = useField('cvc', null, {
    validateOnValueUpdate: false
  });

  const {
    value: zipCode,
    errorMessage: zipCodeError,
    validate: validateZip
  } = useField('zipCode', null, {
    validateOnValueUpdate: false
  });

  // Credit card token
  const { value: tokenMode } = useField('tokenMode', null, {
    validateOnValueUpdate: false,
    initialValue: CARD_TOKEN_MODES.CHARGE
  });

  const { value: savedPaymentToken } = useField('savedPaymentToken', null, {
    validateOnValueUpdate: false
  });

  const { value: securedTokenCvc, errorMessage: securedTokenCvcError } =
    useField('securedTokenCvc', null, {
      validateOnValueUpdate: false
    });

  // Credit card token
  const { value: laneId, errorMessage: laneIdError } = useField(
    'laneId',
    null,
    {
      validateOnValueUpdate: false
    }
  );

  // Bank account fields

  const {
    value: accountNumber,
    errorMessage: accountNumberError,
    validate: validateAccountNumber
  } = useField('accountNumber', null, { validateOnValueUpdate: false });
  const {
    value: routingNumber,
    errorMessage: routingNumberError,
    validate: validateRoutingNumber
  } = useField('routingNumber', null, {
    validateOnValueUpdate: false
  });

  const {
    value: accountType,
    errorMessage: accountTypeError,
    validate: validateAccountType
  } = useField('accountType', null, {
    validateOnValueUpdate: false,
    initialValue: 'CORPORATE_CHECKING'
  });

  // Subscription details

  const {
    value: email,
    errorMessage: emailError,
    validate: validateEmail
  } = useField('email', null, { validateOnValueUpdate: false });

  const { value: emailOption } = useField('emailOption', null, {
    validateOnValueUpdate: false
  });

  // Tip
  const { value: allowTip } = useField('allowTip', null, {
    validateOnValueUpdate: false
  });

  const { value: tipValue } = useField('tipValue', null, {
    validateOnValueUpdate: false
  });

  const { value: tipPercent } = useField('tipPercent', null, {
    validateOnValueUpdate: false
  });

  // // show email field
  // const { value: showEmail } = useField('showEmail', null, {
  //   validateOnValueUpdate: false
  // });

  // sign up user or not
  const { value: savePaymentTokenIsAvailable } = useField(
    'savePaymentTokenIsAvailable',
    null,
    {
      validateOnValueUpdate: false,
      initialValue: false
    }
  );

  const { value: signUpUser } = useField('signUpUser', null, {
    validateOnValueUpdate: false,
    initialValue: false
  });

  const { value: sendEmail } = useField('sendEmail', null, {
    validateOnValueUpdate: false
  });

  // do not allow to change email(when email is predefined from params)
  const { value: emailReadOnly } = useField('emailReadOnly', null, {
    validateOnValueUpdate: false
  });

  // send email field
  const { value: zipMode } = useField('zipMode', null, {
    validateOnValueUpdate: false
  });

  // customer details

  const { value: phoneOption } = useField('phoneOption', null, {
    validateOnValueUpdate: false
  });

  const { value: phoneNumber, validate: validatePhoneNumber } = useField(
    'phoneNumber',
    null,
    {
      validateOnValueUpdate: false
    }
  );

  const { value: showFullName } = useField('showFullName', null, {
    validateOnValueUpdate: false
  });

  const { value: fullNameDetails } = useField('fullNameDetails', null, {
    initialValue: {
      firstName: null,
      lastName: null
    },
    validateOnValueUpdate: false
  });

  const { validate: validateFirstName } = useField(
    'fullNameDetails.firstName',
    null,
    {
      validateOnValueUpdate: false
    }
  );

  const { validate: validateLastName } = useField(
    'fullNameDetails.lastName',
    null,
    {
      validateOnValueUpdate: false
    }
  );

  // billing address data
  // const { value: showBillingAddress } = useField('showBillingAddress', null);

  // const { value: billingAddress } = useField('billingAddress', null, {
  //   initialValue: {
  //     billingAddress: null
  //   },
  //   validateOnValueUpdate: false
  // });

  // useField('billingAddress.billingAddress', null, {
  //   validateOnValueUpdate: false
  // });

  // address data

  const { value: address } = useField('address', null, {
    initialValue: {
      country: DEFAULT_COUNTRY,
      firstName: null,
      lastName: null,
      address: null,
      address_2: null,
      city: null,
      zip: null,
      state: null
    },
    validateOnValueUpdate: false
  });

  const { value: addressOptions } = useField('addressOptions', null, {
    initialValue: false
  });

  // billing address

  const { value: billingAddress } = useField('billingAddress', null, {
    initialValue: {
      country: DEFAULT_COUNTRY,
      firstName: null,
      lastName: null,
      address: null,
      address_2: null,
      city: null,
      zip: null,
      state: null
    },
    validateOnValueUpdate: false
  });

  const { value: billingOptions } = useField('billingOptions', null, {
    initialValue: false
  });

  useField('address.country', null, {
    validateOnValueUpdate: false
  });

  useField('address.firstName', null, {
    validateOnValueUpdate: false
  });

  useField('address.lastName', null, {
    validateOnValueUpdate: false
  });

  useField('address.address', null, {
    validateOnValueUpdate: false
  });

  useField('address.address_2', null, {
    validateOnValueUpdate: false
  });

  useField('address.city', null, {
    validateOnValueUpdate: false
  });

  useField('address.zip', null, {
    validateOnValueUpdate: false
  });

  useField('address.state', null, {
    validateOnValueUpdate: false
  });

  // billing

  useField('billingAddress.country', null, {
    validateOnValueUpdate: false
  });

  useField('billingAddress.firstName', null, {
    validateOnValueUpdate: false
  });

  useField('billingAddress.lastName', null, {
    validateOnValueUpdate: false
  });

  useField('billingAddress.address', null, {
    validateOnValueUpdate: false
  });

  useField('billingAddress.address_2', null, {
    validateOnValueUpdate: false
  });

  useField('billingAddress.city', null, {
    validateOnValueUpdate: false
  });

  useField('billingAddress.zip', null, {
    validateOnValueUpdate: false
  });

  useField('billingAddress.state', null, {
    validateOnValueUpdate: false
  });

  return {
    validate,
    selectedCryptoType,
    selectedCryptoTypeError,
    cardNumber,
    date,
    cvc,
    cardNumberError,
    dateError,
    cvcError,
    validateDate,
    validateCvc,
    validateZip,
    validateCardNumber,

    setErrors,
    zipCode,
    zipCodeError,
    accountNumber,
    accountNumberError,
    validateAccountNumber,
    routingNumber,
    routingNumberError,
    validateRoutingNumber,
    accountType,
    accountTypeError,
    validateAccountType,
    validateFirstName,
    validateLastName,
    validatePhoneNumber,

    paymentMethod,
    email,
    emailError,
    validateEmail,
    paymentType,
    paymentOptions,

    savedPaymentToken,
    securedTokenCvc,
    securedTokenCvcError,
    tokenMode,

    laneId,
    laneIdError,

    //tip
    allowTip,
    tipValue,
    tipPercent,

    // // Show email field
    // showEmail,

    // send email
    sendEmail,

    // sign up user
    signUpUser,
    savePaymentTokenIsAvailable,

    // do not allow to change email
    emailReadOnly,
    emailOption,

    // make zipcode required or hidden
    zipMode,

    // address
    addressOptions,
    address,
    errors,

    billingOptions,

    billingAddress,
    // showBillingAddress,

    // customer details
    fullNameDetails,
    showFullName,

    phoneOption,
    phoneNumber
  };
}
