<template>
  <div>
    <div id="quill" class="quill"></div>
  </div>
</template>

<script>
import Quill from 'quill';
import { Hr } from '@/helpers/quill/divider.js';

import { onMounted, ref, toRef } from '@vue/runtime-core';
const Size = Quill.import('attributors/style/size');

import 'quill/dist/quill.core.css';
import 'quill/dist/quill.snow.css';

const SIZES = [
  '10px',
  '12px',
  '14px',
  '16px',
  '18px',
  '20px',
  '22px',
  '24px',
  '26px',
  '28px',
  '30px'
];
Size.whitelist = SIZES;
Quill.register(Size, true);
Quill.register({
  'formats/hr': Hr
});

export default {
  name: 'ProductsDescription',
  props: {
    description: {
      type: Object,
      default: null
    }
  },
  setup(props) {
    const quill = ref(null);
    const description = toRef(props, 'description');

    onMounted(() => {
      quill.value = new Quill('#quill', {
        modules: {
          toolbar: false
        },
        placeholder: '',
        theme: 'snow',
        readOnly: true
      });

      quill.value.setContents(description.value);
    });

    return {};
  }
};
</script>

<style lang="scss" scoped>
.quill {
  border: none;
  font-size: 14px;
}

::v-deep(.quill) {
  .ql-editor {
    padding: 0 !important;
  }
}
</style>
