import { ref, reactive } from '@vue/reactivity';
import useService from '@/composables/common/services';
import { countdown } from '@/helpers';
import { DEFAULT_PAYMENT_ERROR_MESSAGE } from '@/config/constants.js';
import { getAddressFields } from '@/helpers/index.js';

const DEFAULT_TIMEOUT_MINUTES = 5;

export default function (
  selectedCryptoType,
  amount,
  email,
  newWindow,
  tipValue,
  allowTip,
  address,
  addressOptions,
  billingAddress,
  billingOptions
) {
  const qrImage = ref(null);
  const cryptoData = ref({});
  const cryptoError = ref(null);
  const { apiService } = useService();
  const timer = reactive({
    minutes: null,
    seconds: null
  });
  const timerId = ref(null);
  const intervalId = ref(null);

  const showCryptoPaymentModal = ref(false);

  const getLogoUrl = (cryptocode) => {
    return `https://tripleplaypay.com/static/images/coins/32/color/${cryptocode.toLowerCase()}.png`;
  };

  const processCryptoPayment = (paymentType) => {
    cryptoError.value = null;

    const data = {
      crypto: selectedCryptoType.value?.value,
      amount: amount.value,
      email: email.value,
      logo: getLogoUrl(selectedCryptoType.value?.value)
    };

    if (addressOptions.value) {
      data.address = getAddressFields(addressOptions.value, address.value);
    }

    if (billingOptions.value) {
      data.billingAddress = getAddressFields(
        billingOptions.value,
        billingAddress.value
      );
    }

    if (allowTip.value && tipValue.value) {
      data.tip = tipValue.value;
    }

    if (!newWindow.value) {
      data.source = 'iframe';
    }

    return apiService
      .post(paymentType, data)
      .then((res) => {
        const qr = res.message.qr;
        qrImage.value = 'data:image/png;base64, ' + qr;

        if (qr) {
          cryptoData.value = {
            qr: 'data:image/png;base64, ' + res.message.qr,
            address: res.message.cryptoaddress,
            currency: res.message.currencyname,
            expectedamount: res.message.expectedamount,
            multiplier: res.message.multiplier,
            htmlsymbol: res.message.htmlsymbol,
            cryptocode: selectedCryptoType.value?.value,
            externaltransactionid: res.message.externaltransactionid,
            status: 'pending'
          };
        } else {
          cryptoData.value = {
            status: 'error'
          };
        }

        showCryptoPaymentModal.value = true;
        // subscribeForPaymentCallback('a14ecd66-83c5-466c-962d-226d2adfe92c');

        if (cryptoData.value.status === 'error') {
          return;
        }

        subscribeForPaymentCallback(res.message.externaltransactionid);

        timerId.value = countdown(
          (time) => {
            timer.seconds = time.seconds;
            timer.minutes = time.minutes;
          },
          {
            minutes: +res.message.timeout || DEFAULT_TIMEOUT_MINUTES
          },
          () => {
            showCryptoPaymentModal.value = false;
            clearInterval(intervalId.value);

            cryptoData.value = {
              qr: null,
              address: null,
              currency: null,
              expectedamount: null,
              multiplier: null,
              htmlsymbol: null,
              cryptocode: null,
              externaltransactionid: null
            };
          }
        );
      })
      .catch(() => {
        cryptoError.value = DEFAULT_PAYMENT_ERROR_MESSAGE;
      });
  };

  const subscribeForPaymentCallback = (transactionId) => {
    const websocket = apiService.findService('websocket');

    const data = {
      id: {
        externaltransactionid: transactionId
      }
    };
    intervalId.value = setInterval(() => {
      websocket
        .post('callback', data, 1000 * 60 * 5)
        .then((res) => {
          const meta = res[1];

          if (meta.iscanceled) {
            clearInterval(intervalId.value);
            clearTimeout(timerId.value);

            cryptoData.value = {
              ...cryptoData.value,
              txstatus: meta.txstatus,
              status: 'error',
              txhash: meta.txhash,
              overunderamount: meta.overunderamount,
              netamount: meta.netamount,
              canceledreason: meta.canceledreason,
              iscanceled: meta.iscanceled
            };

            return;
          }

          if (!meta || !meta.txstatus) {
            return;
          }

          clearInterval(intervalId.value);
          clearTimeout(timerId.value);

          if (meta.txstatus === 'Completed' || meta.txstatus === 'Confirming') {
            cryptoData.value = {
              ...cryptoData.value,
              txstatus: meta.txstatus,
              status: 'success',
              txhash: meta.txhash,
              overunderamount: meta.overunderamount,
              netamount: meta.netamount
            };

            return;
          }

          cryptoData.value = {
            ...cryptoData.value,
            status: 'error'
          };
        })
        .catch((err) => {
          if (!err) {
            return;
          }

          clearInterval(intervalId.value);
          clearTimeout(timerId.value);

          cryptoData.value = {
            ...cryptoData.value,
            status: 'error'
          };
        });
    }, 2000);
  };

  return {
    showCryptoPaymentModal,
    qrImage,
    cryptoData,
    timer,
    timerId,
    cryptoError,

    processCryptoPayment
  };
}
