import { removeUpperFromCssProperty } from '@/helpers/index.js';

const getStyleVars = (avaialbleKeys, styles = {}) => {
  const properies = {};

  avaialbleKeys.forEach((keyName) => {
    properies[`--custom-${keyName}`] = styles[keyName];
  });

  return properies;
};

const getPossibleKeyNames = (keyName, states, extend) => {
  const currExtend = extend || [];
  const currStates = states || [];

  const result = [keyName];
  currStates.forEach((state) => {
    result.push(`${state.name}-${keyName}`);
  });

  currExtend.forEach((prop) => {
    result.push(`${keyName}-${prop}`);
  });

  return result;
};

export const getCustomClasses = (style = {}, availableProperties) => {
  const classes = {};

  availableProperties.forEach((prop) => {
    const possibleKeys = getPossibleKeyNames(
      prop.name,
      prop.states,
      prop.extend
    );
    const availableKeys = getAvailableKeys(style, possibleKeys);

    if (!availableKeys.length) {
      return;
    }

    availableKeys.forEach((key) => {
      classes[`has-custom-${key}`] = true;
    });
  });

  return classes;
};

const getAvailableKeys = (style, keys) => {
  const result = [];

  for (let key of keys) {
    if (style[key]) {
      result.push(key);
    }
  }

  return result;
};

export const getCustomStyle = (style = {}, keys) => {
  let resultingStyle = {};

  keys.forEach((key) => {
    const possibleKeys = getPossibleKeyNames(key.name, key.states, key.extend);
    const availableKeys = getAvailableKeys(style, possibleKeys);

    if (!availableKeys.length) {
      return;
    }

    resultingStyle = {
      ...getStyleVars(availableKeys, style),
      ...resultingStyle
    };
  });

  return resultingStyle;
};

const getKeyWithPrefix = (key, prefix) => {
  if (!prefix) {
    return key;
  }

  return `${prefix}-${key}`;
};

const expandStyleObject = (style) => {
  if (!style) {
    return {};
  }

  let result = {};

  for (let key in style) {
    if (typeof style[key] !== 'object') {
      result[key] = style[key];
      continue;
    }

    result = {
      ...result,
      ...expandStyleObject(style[key])
    };
  }

  return result;
};

export const getCurrentStyle = (style = {}, prefix = '') => {
  if (!style) {
    return {};
  }

  const currStyle = { ...style };

  Object.keys(style).forEach((key) => {
    if (typeof currStyle[key] === 'object' && currStyle[key]) {
      currStyle[key] = getCurrentStyle(currStyle[key], key);
      return;
    }

    const formattedKey = removeUpperFromCssProperty(key);
    const newKey = getKeyWithPrefix(formattedKey, prefix);

    if (newKey !== key) {
      currStyle[newKey] = style[key];

      delete currStyle[key];
    }
  });

  return expandStyleObject(currStyle);
};
