<template>
  <div class="mb-2">
    <p class="fw-500 fz-14 color-dark mb-2">Billing address</p>

    <div>
      <base-input
        v-model="state.billingAddress"
        :error="errors['billingAddress.billingAddress']"
        :custom-style="customStyle"
        placeholder="169 Pinnick Street, Waye, New Jersey, 07477"
        @update:modelValue="handleUpdate('billingAddress', $event)"
      />
    </div>
  </div>
</template>

<script>
import BaseInput from '@/components/base/BaseInput.vue';
import { reactive, toRefs } from '@vue/reactivity';
import { onMounted } from '@vue/runtime-core';

export default {
  name: 'AddressSection',
  components: {
    BaseInput
  },
  props: {
    modelValue: {
      type: Object,
      default: () => {}
    },
    errors: {
      type: Object,
      default: () => {}
    },
    customStyle: {
      type: Object,
      default: () => {}
    }
  },
  emits: ['update:modelValue'],
  setup(props, { emit }) {
    const { modelValue } = toRefs(props);

    const state = reactive({
      billingAddress: null
    });

    const handleUpdate = (key, value) => {
      emit('update:modelValue', {
        ...modelValue.value,
        [key]: value
      });
    };

    onMounted(() => {
      for (const key in modelValue.value) {
        state[key] = modelValue.value[key];
      }
    });

    return {
      state,
      handleUpdate
    };
  }
};
</script>

<style lang="scss" scoped>
.address-box {
  background-color: #fafafa;
  padding: 5px;
  border-radius: 5px;
}
</style>
