<template>
  <div>
    <p class="fw-500 fz-14 color-dark mb-2">Full name</p>

    <div class="d-flex">
      <base-input
        :model-value="modelValue.firstName"
        :custom-style="customStyle"
        :autocomplete="autocomplete ? 'first_name' : 'off'"
        :error="errors['fullNameDetails.firstName']"
        name="first_name"
        placeholder="First name"
        class="w-50 value-input mr-2"
        @update:modelValue="handleUpdate('firstName', $event)"
        @blur="$emit('blur:firstName')"
      />

      <base-input
        :model-value="modelValue.lastName"
        :custom-style="customStyle"
        :autocomplete="autocomplete ? 'last_name' : 'off'"
        :error="errors['fullNameDetails.lastName']"
        name="last_name"
        placeholder="Last name"
        class="w-50 value-input"
        @update:modelValue="handleUpdate('lastName', $event)"
        @blur="$emit('blur:lastName')"
      />
    </div>
  </div>
</template>

<script>
import BaseInput from '@/components/base/BaseInput.vue';
import { toRefs } from 'vue';

export default {
  name: 'CustomerNameSection',
  components: {
    BaseInput
  },
  props: {
    autocomplete: {
      type: Boolean,
      default: false
    },
    modelValue: {
      type: Object,
      default: () => {}
    },
    errors: {
      type: Object,
      default: () => {}
    },
    customStyle: {
      type: Object,
      default: () => {}
    }
  },
  emits: ['update:modelValue', 'blur:firstName', 'blur:lastName'],
  setup(props, { emit }) {
    const { modelValue } = toRefs(props);

    const handleUpdate = (key, value) => {
      emit('update:modelValue', {
        ...modelValue.value,
        [key]: value
      });
    };

    return {
      handleUpdate
    };
  }
};
</script>
