import useService from '@/composables/common/services';
// import httpService from '@/services/api-service/http.js';

export default function (savePaymentTokenIsAvailable) {
  const { apiService } = useService();

  const http = apiService.findService('http');

  // const savePaymentTokenIsAvailable = ref(true);

  const getBankDetails = (data) => {
    return `accountNumber=${data.accountNumber}&routingNumber=${data.routingNumber}&TYPE=${data.type}`;
  };

  const getCardDetails = (data) => {
    return `cc=${data.cc}&mm=${data.mm}&yy=${data.yy}&zip=${data.zip}&cvv=${data.cvc}`;
  };

  const getMetaValue = (data) => {
    if (!data.meta) {
      return '';
    }

    if (typeof data.meta !== 'object') {
      return `&meta=${data.meta}`;
    }

    if (!Object.keys(data.meta).length) {
      return '';
    }

    return `&meta=${JSON.stringify(data.meta)}`;
  };

  const getMetaIdValue = (data) => {
    if (!data.meta_id) {
      return '';
    }

    if (!Array.isArray(data.meta_id)) {
      return `&meta_id=${data.meta_id}`;
    }

    return `&meta_id=${JSON.stringify(data.meta_id)}`;
  };

  const saveCreditCardToken = (data) => {
    let result = '?';

    if (data.id) {
      result += `id=${data.id}`;
    }

    if (data.cc) {
      result += `&${getCardDetails(data)}`;
    }

    if (data.accountNumber) {
      result += `&${getBankDetails(data)}`;
    }

    if (data.meta) {
      result += `${getMetaValue(data)}`;
    }

    if (data.meta_id) {
      result += `${getMetaIdValue(data)}`;
    }

    return http.get(`wallet${result}`);
  };

  return {
    saveCreditCardToken,
    savePaymentTokenIsAvailable
  };
}
