<template>
  <div>
    <p class="fw-500 fz-14 color-dark mb-2">{{ label }}</p>

    <div
      v-if="
        typeof addressOptions === 'object' && !Array.isArray(addressOptions)
      "
      class="address-box"
    >
      <p
        v-if="addressOptions.first_name || addressOptions.last_name"
        class="fz-14 color-grey-dark"
      >
        {{ addressOptions.first_name }} {{ addressOptions.last_name }}
      </p>

      <p v-if="addressOptions.address" class="fz-14 color-grey-medium">
        {{ addressOptions.address }}
      </p>

      <p v-if="addressOptions.address_2" class="fz-14 color-grey-medium">
        {{ addressOptions.address_2 }}
      </p>

      <p
        v-if="
          addressOptions.city ||
          addressOptions.state ||
          addressOptions.country ||
          addressOptions.zip
        "
        class="fz-14 color-grey-medium"
      >
        {{ addressBox }}
      </p>
    </div>

    <div v-else>
      <div v-if="renderRow('TWO')" class="d-flex">
        <base-input
          v-if="renderOption(ADDRESS_FIEDLS.FIRST_NAME)"
          v-model="state.firstName"
          :error="errors[`${errorKey}.firstName`]"
          :class="getClasses(ADDRESS_FIEDLS.FIRST_NAME)"
          :custom-style="customStyle"
          placeholder="First name"
          @update:modelValue="handleUpdate('firstName', $event)"
        />
        <base-input
          v-if="renderOption(ADDRESS_FIEDLS.LAST_NAME)"
          v-model="state.lastName"
          :error="errors[`${errorKey}.lastName`]"
          :class="getClasses(ADDRESS_FIEDLS.LAST_NAME)"
          :custom-style="customStyle"
          placeholder="Last name"
          @update:modelValue="handleUpdate('lastName', $event)"
        />
      </div>

      <base-dropdown
        v-if="renderOption(ADDRESS_FIEDLS.COUNTRY)"
        v-model="state.country"
        autocomplete
        :allow-custom-value="false"
        :options="countries"
        :error="errors[`${errorKey}.country`]"
        :custom-style="customStyle"
        placeholder="Country"
        class="w-100 mt-2"
        @update:modelValue="handleUpdate('country', $event)"
      />

      <base-input
        v-if="renderOption(ADDRESS_FIEDLS.ADDRESS)"
        v-model="state.address"
        :error="errors[`${errorKey}.address`]"
        :custom-style="customStyle"
        placeholder="Address Line 1"
        class="mt-2"
        @update:modelValue="handleUpdate('address', $event)"
      />

      <base-input
        v-if="renderOption(ADDRESS_FIEDLS.ADDRESS_2)"
        v-model="state.address_2"
        :custom-style="customStyle"
        placeholder="Address Line 2"
        class="mt-2"
        @update:modelValue="handleUpdate('address_2', $event)"
      />

      <div v-if="renderRow('FOUR')" class="d-flex mt-2">
        <base-input
          v-if="renderOption(ADDRESS_FIEDLS.CITY)"
          v-model="state.city"
          :error="errors[`${errorKey}.city`]"
          :class="getClasses(ADDRESS_FIEDLS.CITY)"
          :custom-style="customStyle"
          placeholder="City"
          @update:modelValue="handleUpdate('city', $event)"
        />
        <base-input
          v-if="renderOption(ADDRESS_FIEDLS.STATE)"
          v-model="state.state"
          :error="errors[`${errorKey}.state`]"
          :class="getClasses(ADDRESS_FIEDLS.STATE)"
          :custom-style="customStyle"
          placeholder="State"
          @update:modelValue="handleUpdate('state', $event)"
        />
        <base-input
          v-if="renderOption(ADDRESS_FIEDLS.ZIP)"
          v-model="state.zip"
          :error="errors[`${errorKey}.zip`]"
          :class="getClasses(ADDRESS_FIEDLS.ZIP)"
          :custom-style="customStyle"
          placeholder="Zip code"
          @update:modelValue="handleUpdate('zip', $event)"
        />
      </div>
    </div>
  </div>
</template>

<script>
import COUNTRIES from '@/config/countries.json';

import BaseInput from '@/components/base/BaseInput.vue';
import BaseDropdown from '@/components/base/BaseDropdown.vue';
import { computed, reactive, toRefs } from '@vue/reactivity';
import { onMounted } from '@vue/runtime-core';

const ADDRESS_FIEDLS = {
  COUNTRY: 'country',
  FIRST_NAME: 'first_name',
  LAST_NAME: 'last_name',
  ADDRESS: 'address',
  ADDRESS_2: 'address_2',
  CITY: 'city',
  STATE: 'state',
  ZIP: 'zip'
};

const ROWS = {
  ONE: [ADDRESS_FIEDLS.COUNTRY],
  TWO: [ADDRESS_FIEDLS.FIRST_NAME, ADDRESS_FIEDLS.LAST_NAME],
  THREE: [ADDRESS_FIEDLS.ADDRESS_2],
  FOUR: [ADDRESS_FIEDLS.STATE, ADDRESS_FIEDLS.CITY, ADDRESS_FIEDLS.ZIP]
};

const WIDTH_CLASSES_BY_LENGTH = {
  l_1: 'w-100',
  l_2: 'w-50',
  l_3: 'w-30'
};

const generateAddressBox = (data) => {
  let result = '';

  if (data.city) {
    result += `${data.city}, `;
  }

  if (data.state) {
    result += `${data.state}, `;
  }

  if (data.zip) {
    result += `${data.zip}, `;
  }

  if (data.country) {
    result += `${data.country}`;
  }

  return result;
};

export default {
  name: 'AddressSection',
  components: {
    BaseInput,
    BaseDropdown
  },
  props: {
    label: {
      type: String,
      default: null
    },
    errorKey: {
      type: String,
      required: true
    },
    modelValue: {
      type: Object,
      default: () => {}
    },
    errors: {
      type: Object,
      default: () => {}
    },
    customStyle: {
      type: Object,
      default: () => {}
    },
    addressOptions: {
      type: [Boolean, Array, Object],
      default: null
    }
  },
  emits: ['update:modelValue'],
  setup(props, { emit }) {
    const { modelValue, addressOptions } = toRefs(props);

    const state = reactive({
      country: null,
      firstName: null,
      lastName: null,
      address_2: null,
      city: null,
      zip: null,
      state: null
    });

    const countries = computed(() => {
      return COUNTRIES.map((c) => {
        return {
          value: c.code,
          text: c.name
        };
      });
    });

    const addressBox = computed(() => {
      return generateAddressBox(addressOptions.value);
    });

    const handleUpdate = (key, value) => {
      emit('update:modelValue', {
        ...modelValue.value,
        [key]: value
      });
    };

    const renderOption = (option) => {
      // if addressOptions is true,
      // no need to check specific option
      // just render all
      if (addressOptions.value === true) {
        return true;
      }

      if (Array.isArray(addressOptions.value)) {
        return addressOptions.value.includes(option);
      }

      return true;
    };

    const renderRow = (rowName) => {
      return getRowAvailableOptions(ROWS[rowName]);
    };

    const getOptionRow = (option) => {
      for (let key in ROWS) {
        if (ROWS[key].includes(option)) {
          return key;
        }
      }

      return null;
    };

    const getRowAvailableOptions = (row) => {
      let count = 0;

      if (!Array.isArray(addressOptions.value) && addressOptions.value) {
        return row.length;
      }

      addressOptions.value.forEach((o) => {
        if (row.includes(o)) {
          count++;
        }
      });

      return count;
    };

    const getClasses = (option) => {
      const row = getOptionRow(option);

      if (!row) {
        return {};
      }

      let classes = [];
      const rowData = ROWS[row];
      const index = ROWS[row].indexOf(option);

      if (index < rowData.length - 1) {
        classes.push('mr-2');
      }

      const availableOptionsCount = getRowAvailableOptions(rowData);

      classes.push(WIDTH_CLASSES_BY_LENGTH[`l_${availableOptionsCount}`]);

      return classes;
    };

    onMounted(() => {
      for (const key in modelValue.value) {
        state[key] = modelValue.value[key];
      }
    });

    return {
      countries,
      ADDRESS_FIEDLS,
      ROWS,
      addressBox,

      state,
      handleUpdate,
      renderOption,
      renderRow,
      getClasses
    };
  }
};
</script>

<style lang="scss" scoped>
.address-box {
  background-color: #fafafa;
  padding: 5px;
  border-radius: 5px;
}
</style>
