import expiration from 'creditcards/expiration';

export function isRequired(value) {
  if (value && value.trim()) {
    return true;
  }
  return 'Field is required';
}

export const isCardNumberValid = (value) => {
  const currV = value || '';

  const CARD_LENGTH_WITH_SPACES = 14;

  if (currV.length < CARD_LENGTH_WITH_SPACES) {
    return false;
  }

  return true;
};

export function dateIsValid(value) {
  const currV = value || '';
  const month = currV.split('/')[0] || '';
  const monthAsNumber = +month;

  if (monthAsNumber > 12) {
    return false;
  }

  const year = currV.split('/')[1] || '';
  const yearAsNumber = +`20${+year.trim()}`;

  if (expiration.isPast(monthAsNumber, yearAsNumber)) {
    return false;
  }

  return true;
}
