<template>
  <div class="base-tab">
    <div class="header-el">
      <slot name="header"></slot>
    </div>

    <div ref="collapse" class="content collapse" :class="{ show: initialOpen }">
      <slot name="default" />
    </div>
  </div>
</template>

<script>
import Collapse from 'bootstrap/js/dist/collapse';

import {
  onBeforeUnmount,
  onMounted,
  ref,
  toRefs,
  watch
} from '@vue/runtime-core';

export default {
  name: 'BaseTab',
  props: {
    modelValue: {
      type: Boolean,
      default: false
    }
  },
  emits: ['change'],
  setup(props, { emit }) {
    const { modelValue } = toRefs(props);

    const opened = ref(true);
    const collapse = ref(null);
    const collapseEl = ref(null);
    const initialOpen = ref(modelValue.value);

    const handleClose = () => {
      if (!initialOpen.value) {
        return;
      }

      initialOpen.value = false;
    };

    onMounted(() => {
      collapseEl.value = new Collapse(collapse.value, {
        toggle: false
      });

      collapse.value.addEventListener('hidden.bs.collapse', handleClose);
    });

    onBeforeUnmount(() => {
      collapse.value.removeEventListener('hidden.bs.collapse', handleClose);
    });

    watch(
      () => modelValue.value,
      (v) => {
        if (collapseEl.value._isTransitioning) {
          return;
        }

        if (!v) {
          collapseEl.value.hide();
          emit('change', false);
        } else {
          collapseEl.value.show();
          emit('change', true);
        }
      }
    );

    return {
      opened,
      collapse,
      initialOpen
    };
  }
};
</script>

<style lang="scss" scoped>
.header-el {
  user-select: none; /* standard syntax */
  -webkit-user-select: none; /* webkit (safari, chrome) browsers */
  -moz-user-select: none; /* mozilla browsers */
  -khtml-user-select: none; /* webkit (konqueror) browsers */
  -ms-user-select: none; /* IE10+ */
}
</style>
