const DEFAULT_TYPE = 'warning';
const DEFAULT_DELAY = 3000;

const DEFAULT_OPTIONS = () => {
  return {
    title: null,
    text: null,
    delay: null,
    type: null,
    autohide: true,
    canClose: true
  };
};

const toast = {
  state: () => ({
    showToast: false,
    options: DEFAULT_OPTIONS()
  }),
  mutations: {
    show(
      state,
      {
        title,
        text,
        delay = DEFAULT_DELAY,
        autohide = true,
        canClose = true,
        type = DEFAULT_TYPE
      }
    ) {
      state.options = {
        title,
        text,
        delay,
        autohide,
        canClose,
        type
      };

      state.showToast = true;
    },
    close(state) {
      state.options = DEFAULT_OPTIONS();

      state.showToast = false;
    }
  },
  actions: {},
  getters: {},
  namespaced: true
};

export default toast;
