const INTERVALS = ['daily', 'weekly', 'monthly', 'yearly'];
import { PAYMENT_TYPES } from '@/config/constants.js';

export default function (data, paymentType) {
  const { start, end, cycles, interval, frequency, email, trial } = data;

  if (
    !INTERVALS.includes(interval) &&
    paymentType.value === PAYMENT_TYPES.SUBSCRIPTION
  ) {
    console.error('interval value is not valid');
    console.warn(`Available values: ${INTERVALS.join(', ')}`);
  }

  let formattedTrial;
  if (trial) {
    formattedTrial = Math.max(0, trial);
  }

  return {
    start,
    end,
    cycles,
    interval,
    frequency,
    email,
    trial: formattedTrial
  };
}
