import { computed } from 'vue';
import { PAYMENT_METHODS, PAYMENT_TYPES } from '@/config/constants.js';
import { getAddressFields } from '@/helpers/index.js';

export default function (paymentMethod, fields) {
  let valuesData = {};

  if (fields.source) {
    valuesData.source = fields.source;
  } else if (!fields.newWindow.value) {
    valuesData.source = 'iframe';
  }

  const emailValue = computed(() => {
    if (typeof fields.emailFromParams === 'string' && fields.emailFromParams) {
      return fields.emailFromParams;
    }

    if (fields.email.value) {
      return fields.email.value;
    }

    return null;
  });

  const month = computed(() => {
    const value = fields.date.value || '';
    const month = value.split('/')[0] || '';
    return month.trim();
  });

  const year = computed(() => {
    const value = fields.date.value || '';
    const year = value.split('/')[1] || '';
    return year.trim();
  });

  const cardNumber = computed(() => {
    return (fields.cardNumber.value || '').replaceAll(' ', '');
  });

  const addressData = computed(() => {
    if (!fields.addressOptions.value) {
      return {};
    }

    return {
      address: JSON.stringify(
        getAddressFields(fields.addressOptions.value, fields.address.value)
      )
    };
  });

  const billingData = computed(() => {
    if (!fields.billingOptions.value) {
      return {};
    }

    return {
      billingAddress: JSON.stringify(
        getAddressFields(
          fields.billingOptions.value,
          fields.billingAddress.value
        )
      )
    };
  });

  const creditCardData = computed(() => {
    if (paymentMethod.value !== PAYMENT_METHODS.CARD) {
      return {};
    }

    return {
      cc: cardNumber.value,
      mm: month.value,
      yy: year.value,
      cvv: fields.cvc.value
    };
  });

  const bankAccountData = computed(() => {
    if (paymentMethod.value !== PAYMENT_METHODS.BANK) {
      return {};
    }

    return {
      accountNumber: fields.accountNumber.value,
      routingNumber: fields.routingNumber.value,
      TYPE: fields.accountType.value
    };
  });

  const tipsData = computed(() => {
    if (!fields.allowTip.value || !+fields.tipValue.value) {
      return {};
    }

    return {
      tip: fields.tipValue.value
    };
  });

  const terminalData = computed(() => {
    if (paymentMethod.value !== PAYMENT_METHODS.TERMINAL) {
      return {};
    }

    const data = {
      laneId: fields.laneId.value
    };

    return data;
  });

  const savedTokenData = computed(() => {
    if (!fields.savedPaymentToken.value) {
      return {};
    }

    const data = {
      token: fields.savedPaymentToken.value.token
    };

    if (fields.securedTokenCvc.value) {
      data.cvc = fields.securedTokenCvc.value;
    }

    return data;
  });

  const emailData = computed(() => {
    if (!emailValue.value) {
      return {};
    }

    return {
      email: emailValue.value
    };
  });

  const zipData = computed(() => {
    if (!fields.zipCode.value) {
      return {};
    }

    return {
      zip: fields.zipCode.value
    };
  });

  const amountData = computed(() => {
    if (!fields.amount.value) {
      return {
        amount: (0).toFixed(2)
      };
    }

    return {
      amount: fields.amount.value
    };
  });

  const feeData = computed(() => {
    if (!fields.fee) {
      return {};
    }

    return {
      fee: fields.fee
    };
  });

  const metaData = computed(() => {
    if (!fields.meta) {
      return {};
    }

    if (typeof fields.meta === 'object' && !Object.keys(fields.meta).length) {
      return {};
    }

    return {
      meta: fields.meta
    };
  });

  const signUpUser = computed(() => {
    if (!fields.signUpUser.value) {
      return {};
    }

    return {
      signUpUser: fields.signUpUser.value
    };
  });

  const metaIdData = computed(() => {
    if (!fields.meta_id) {
      return {};
    }

    if (typeof fields.meta_id === 'object' && !Array.isArray(fields.meta_id)) {
      return {};
    }

    if (Array.isArray(fields.meta_id) && !fields.meta_id.length) {
      return {};
    }

    return {
      meta_id: fields.meta_id
    };
  });

  const fullNameDetailsData = computed(() => {
    if (
      !fields.fullNameDetails.value.firstName &&
      !fields.fullNameDetails.value.lastName
    ) {
      return {};
    }

    return {
      firstName: fields.fullNameDetails.value.firstName,
      lastName: fields.fullNameDetails.value.lastName
    };
  });

  const phoneNumberData = computed(() => {
    if (!fields.phoneNumber.value) {
      return {};
    }

    return {
      phoneNumber: fields.phoneNumber.value
    };
  });

  // const billingAddressData = computed(() => {
  //   if (!fields.billingAddress.value?.billingAddress) {
  //     return {};
  //   }

  //   return {
  //     billingAddress: fields.billingAddress.value?.billingAddress
  //   };
  // });

  const getSubscriptionPayment = () => {
    let payment = null;

    if (!Object.keys(savedTokenData.value).length) {
      payment = {
        cc: cardNumber.value,
        mm: month.value,
        yy: year.value,
        cvv: fields.cvc.value
      };
    } else {
      payment = { ...savedTokenData.value };
    }

    if (fields.zipCode.value) {
      payment.zip = fields.zipCode.value;
    }

    return JSON.stringify(payment);
  };

  const getSubscriptionData = () => {
    if (fields.paymentType.value !== PAYMENT_TYPES.SUBSCRIPTION) {
      return {};
    }

    const data = {
      // email: emailValue.value,
      start: fields.start,
      cycles: fields.cycles,
      interval: fields.interval,
      frequency: fields.frequency,
      ...emailData.value,
      ...amountData.value
    };

    if (+fields.trial) {
      data.trial = +fields.trial;
    }

    data.payment = getSubscriptionPayment();

    return data;
  };

  const data = computed(() => {
    let data;

    if (fields.paymentType.value === PAYMENT_TYPES.SUBSCRIPTION) {
      data = {
        ...valuesData,
        ...getSubscriptionData(),
        ...tipsData.value,
        ...addressData.value,
        ...billingData.value,
        ...feeData.value,
        ...metaData.value,
        ...metaIdData.value,
        ...fullNameDetailsData.value,
        ...signUpUser.value,
        ...phoneNumberData.value
        // ...billingAddressData.value
      };

      return data;
    }

    data = {
      ...valuesData,
      ...amountData.value,
      ...addressData.value,
      ...billingData.value,
      ...creditCardData.value,
      ...bankAccountData.value,
      ...tipsData.value,
      ...terminalData.value,
      ...emailData.value,
      ...zipData.value,
      ...savedTokenData.value,
      ...feeData.value,
      ...metaData.value,
      ...metaIdData.value,
      ...fullNameDetailsData.value,
      ...signUpUser.value,
      ...phoneNumberData.value
      // ...billingAddressData.value
    };

    return data;
  });

  return {
    data
  };
}
