const { DateTime } = require('luxon');
import {
  PAYMENT_TYPES,
  CARD_TOKEN_MODES,
  PAYMENT_METHODS
} from '@/config/constants.js';

export const splitBySpaces = (value, groupLength = 4) => {
  let result = '';

  for (let i = 0; i < value.length; i++) {
    if (i % groupLength === 0 && i !== 0) {
      result += ` ${value[i]}`;
      continue;
    }

    result += value[i];
  }

  return result;
};

export const removeNotDigitCharacters = (value) => {
  const currV = value || '';
  let result = '';

  for (let i = 0; i < currV.length; i++) {
    const currentChar = +currV[i];

    if (currentChar === 0 || currentChar) {
      result += currentChar;
    }
  }

  return result;
};

export const formatCreditCardNumber = (v) => {
  const value = v || '';
  const withoutSpaces = value.replaceAll(' ', '');
  const onlyDigit = removeNotDigitCharacters(withoutSpaces);

  return splitBySpaces(onlyDigit);
};

const addSlash = (currValue) => {
  const currV = currValue || '';
  let result = '';

  for (let i = 0; i < currV.length; i++) {
    if (i === 1) {
      result += `${currV[i]} / `;
      continue;
    }

    result += currV[i];
  }

  return result;
};

export const getValueWithSlash = (currValue, prevValue) => {
  const value = currValue || '';
  const withoutSpaces = value.replaceAll(' ', '');
  const withoutSlash = withoutSpaces.replaceAll('/', '');
  const onlyDigit = removeNotDigitCharacters(withoutSlash);

  const prevValueWithoutSpaces = (prevValue || '').replaceAll(' ', '');
  const prevValueWithoutSlash = prevValueWithoutSpaces.replaceAll('/', '');

  let endValue;

  const THREE_DIGITS_EXISTS_LENGTH = 3;

  if (
    prevValueWithoutSlash.length === THREE_DIGITS_EXISTS_LENGTH &&
    prevValueWithoutSlash.length > onlyDigit.length
  ) {
    endValue = onlyDigit;
  } else {
    endValue = addSlash(onlyDigit);
  }

  return endValue;
};

export const shadeColor = (color, percent) => {
  var R = parseInt(color.substring(1, 3), 16);
  var G = parseInt(color.substring(3, 5), 16);
  var B = parseInt(color.substring(5, 7), 16);

  R = parseInt((R * (100 + percent)) / 100);
  G = parseInt((G * (100 + percent)) / 100);
  B = parseInt((B * (100 + percent)) / 100);

  R = R < 255 ? R : 255;
  G = G < 255 ? G : 255;
  B = B < 255 ? B : 255;

  var RR = R.toString(16).length == 1 ? '0' + R.toString(16) : R.toString(16);
  var GG = G.toString(16).length == 1 ? '0' + G.toString(16) : G.toString(16);
  var BB = B.toString(16).length == 1 ? '0' + B.toString(16) : B.toString(16);

  return '#' + RR + GG + BB;
};

export const removeUpperFromCssProperty = (prop) => {
  let result = '';

  for (let i = 0; i < prop.length; i++) {
    if (prop[i] !== prop[i].toUpperCase() || prop[i] === '-') {
      result += prop[i];
      continue;
    }

    result += `-${prop[i].toLowerCase()}`;
  }

  return result;
};

export const countdown = (
  callback,
  { hours = 0, minutes = 0, seconds = 0 },
  onComplete
) => {
  const now = DateTime.now();
  let futureDate = now.plus({
    hours: hours,
    minutes: minutes,
    seconds: seconds
  });

  futureDate = futureDate.minus({
    second: 1
  });

  const handleCallback = () => {
    const diff = futureDate.diff(now, ['hours', 'minutes', 'seconds']);

    callback(diff.toObject());
  };

  handleCallback();

  const intervalId = setInterval(() => {
    if (futureDate <= now) {
      clearInterval(intervalId);
      onComplete();
      return;
    }

    futureDate = futureDate.minus({
      second: 1
    });

    handleCallback();
  }, 1000);

  return intervalId;
};

const BRACES_IN_BASE64 = 'e30=';

export const getParsedData = (data) => {
  const queryData = data || BRACES_IN_BASE64;

  const parsed = JSON.parse(decodeURIComponent(atob(queryData)));

  return parsed;
};

export const checkSendUserEmail = ({ initialEmailValue, paymentType }) => {
  if (paymentType === PAYMENT_TYPES.SUBSCRIPTION) {
    return true;
  }

  if (initialEmailValue === false) {
    return false;
  }

  return true;
};

export const checkReadOnlyEmail = ({ initialEmailValue }) => {
  if (typeof initialEmailValue === 'string' && initialEmailValue) {
    return true;
  }

  return false;
};

// first_name => firstName
export const snakeCaseToCamelCase = (value) => {
  let str = '';

  for (let i = 0; i < value.length; i++) {
    if (value[i] === '_') {
      // if next symbol is digit, still use underscore
      if (value[i + 1] && +value[i + 1]) {
        str += value[i];
      }

      continue;
    }

    if (value[i - 1] && value[i - 1] === '_') {
      str += value[i].toUpperCase();
      continue;
    }

    str += value[i];
  }

  console.log(str);

  return str;
};

export const getAddressFields = (options, data) => {
  if (options === true) {
    return data;
  }

  if (Array.isArray(options)) {
    return data;
  }

  if (typeof options === 'object' && options) {
    return options;
  }

  return null;
};

export const debounce = (f, delay) => {
  let timerId;

  return function () {
    clearTimeout(timerId);
    timerId = setTimeout(() => {
      f();
    }, delay);
  };
};

export const LightenDarkenColor = (col, amt) => {
  var num = parseInt(col, 16);
  var r = (num >> 16) + amt;
  var b = ((num >> 8) & 0x00ff) + amt;
  var g = (num & 0x0000ff) + amt;
  var newColor = g | (b << 8) | (r << 16);
  return newColor.toString(16);
};
