<template>
  <apple-pay-button 
    v-if="supported" 
    class="apple-pay-button" 
    buttonstyle="black" 
    type="buy" 
    locale="en-US" 
    style="-webkit-appearance: -apple-pay-button;" 
    @click="applePayButtonClicked">
  </apple-pay-button>
</template>

<script>
  export default {
    name: "ApplePay",
    props: {
      totalPrice: {
        type: String,
        default: "0.00",
        required: true,
      },
    },
    data() {
      return {
        supportedMethods: "https://apple.com/apple-pay",
        countryCode: "US",
        currencyCode: "USD",
        version: 12,
        merchantIdentifier: "merchant.com.tripleplaypay.triple",
        merchantCapabilities: ["supports3DS", "supportsCredit", "supportsDebit"],
        supportedNetworks: ["visa", "masterCard", "amex", "discover"],
        total: {
          label: "Demo (Card is not charged)",
          type: "final",
          amount: this.totalPrice
        },
        request: null,
        requestError: null
      };
    },
    computed: {
      supported() {
        return window.ApplePaySession && window.ApplePaySession.canMakePayments();
      }
    },
    methods: {
      applePayButtonClicked() {
        if (!window.PaymentRequest)
          return;

        try {
            const request = new PaymentRequest(window.supportedMethods, window.paymentDetails, window.paymentOptions);

            request.onmerchantvalidation = function (event) {
                // Have your server fetch a payment session from event.validationURL.
                const sessionPromise = window.fetchPaymentSession(event.validationURL);
                event.complete(sessionPromise);
            };

            const response = request.show();
            const status = window.processResponse(response);
            response.complete(status);
            this.$emit("paymentData", response);
        } catch (e) {
            console.log("Error: " + e.message);
        }
      }
    },
  }
</script>