import { computed } from 'vue';
const { DateTime } = require('luxon');

import { PAYMENT_TYPES, INTERVAL_OPTIONS } from '@/config/constants.js';

export const INTERVAL_NOUNS = {
  daily: 'days',
  weekly: 'weeks',
  monthly: 'months',
  yearly: 'years'
};

const PLURALS = {
  SINGLE: {
    [INTERVAL_OPTIONS.DAILY]: 'every day',
    [INTERVAL_OPTIONS.WEEKLY]: 'every week',
    [INTERVAL_OPTIONS.MONTHLY]: 'every month',
    [INTERVAL_OPTIONS.YEARLY]: 'every year'
  },
  MANY: {
    [INTERVAL_OPTIONS.DAILY]: 'every {n} days',
    [INTERVAL_OPTIONS.WEEKLY]: 'every {n} weeks',
    [INTERVAL_OPTIONS.MONTHLY]: 'every {n} months',
    [INTERVAL_OPTIONS.YEARLY]: 'every {n} years'
  }
};

const getPluralText = ({ frequency, interval }) => {
  if (Number(frequency) === 1) {
    return PLURALS.SINGLE[interval];
  }

  return PLURALS.MANY[interval].replace('{n}', frequency);
};

const DEFAULT_PAY_TEXT = 'Pay ${amount}';

export default function ({
  amount,
  paymentType,
  frequency,
  interval,
  trial,
  start,
  tipValue,
  parsed
}) {
  const payBtnText = (parsed.payBtnText || DEFAULT_PAY_TEXT).trim();

  const amountFormatted = computed(() => {
    let baseValue = Number(amount.value);

    if (!baseValue) {
      return (0).toFixed(2);
    }

    if (+tipValue.value) {
      return (+tipValue.value + baseValue).toFixed(2);
    }

    return baseValue.toFixed(2);
  });

  const getPaymentStartDate = () => {
    let date = DateTime.fromFormat(start, 'yyyy-MM-dd');

    if (trial) {
      date = date.plus({
        days: +trial
      });
    }

    return date.toFormat('MM/dd/yyyy');
  };

  const getOnlyPayText = () => {
    const withoutAmount = payBtnText.replace('{amount}', '') || '';

    return withoutAmount.trim();
  };

  const getReplacedText = () => {
    return payBtnText.replace('{amount}', amountFormatted.value);
  };

  const buttonText = computed(() => {
    if (!amountFormatted.value) {
      return getOnlyPayText();
    }

    // It's charge
    if (paymentType.value === PAYMENT_TYPES.CHARGE) {
      return getReplacedText();
    }

    // else it's subscription
    const intervalText = getPluralText({ frequency, interval });

    return `${getReplacedText()} ${intervalText} starting ${getPaymentStartDate()}`;
  });

  return {
    buttonText
  };
}
